<template>
    <div class="interest">
      <div class="Top">
        <div class="Tmg_left">
          <!--<img :src="assert.assetImg" >-->
          <lazy-image
            :src='favorite.assetImg'
            :placeholder='placeholder'
            :initScore = 'initScore'
            class="imgList_img"
            :imgClass='HisImg'
          />
          <div class="demoName">
            <p>{{favorite.assetName}}</p>
          </div>
          <div class="demo">
            <img :src="favorite.assetImg">
          </div>
        </div>
        <div class="Tmg_right">
          <p>{{tittle.tit}}</p>
          <p>{{tittle.center}}</p>
          <div class="buttons" id="button">{{tittle.butt}}</div>
        </div>
      </div>
      <Category_Old_Activity_Center ref="interestCenter" v-on:pressUp="letUp"></Category_Old_Activity_Center>
      <keyDo v-on:listenKeyCode="keyCode"  ref="keyDo" v-if="isShow"></keyDo>
    </div>
</template>
<style>
  .HisImg{
    width: 100%;
    height: 100%;
    border-radius: 15px 0 0 15px;
    box-sizing: border-box;
  }
</style>
<style scoped="interest">

  .buttons.change{
    border: 4px solid yellow;
    box-sizing: border-box;
    line-height: 54px;
  }
  .Top:after{
    content: '';
    clear: both;
    display: block;
  }
  .Tmg_left{
    width: 600px;
    height: 333px;
    float: left;
    position: relative;
  }
  .demoName{
    width: 600px;
    height: 60px;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0px;
    top: 273px;
    border-radius: 0 0 0px 15px;
    color: #EBEBEB;
    font-size: 20px;
    line-height: 60px;
    text-align: left;
  }
  .demoName p{
    margin-left: 30px;
  }
  .demo{
    width: 130px;
    height: 82px;
    position: absolute;
    left: 460px;
    top: 190px;
    background: rgba(0, 0, 0, 0.5);
  }
  .demo img{
    margin-top: 13px;
    width: 108px;
    height: 110px;
    z-index: 9999999;
  }
  .Tmg_right{
    width: 531px;
    height: 333px;
    float: left;
    background: rgba(255,255,255,0.3);
    position: relative;
    padding-left: 26px;
  }
  .Tmg_right p{
    float: left;
    color: white;
  }
  .Tmg_right p:nth-child(1){
    font-size: 30px;
    margin-top: 14px;
    text-align: left;
  }
  .Tmg_right p:nth-child(2){
    width: 480px;
    margin-top: 22px;
    font-size: 22px;
    text-align: left;
  }
  .buttons{
    width: 182px;
    height: 59px;
    background: #AC4A4A;
    border-radius: 15px;
    border: #E56B6B;
    text-align: center;
    color: white;
    line-height: 59px;
    font-size: 22px;
    position: absolute;
    left: 27px;
    top: 243px;
  }
</style>
<script>
  import Category_Old_Activity_Center from '../interest/Category_Old_Activity_Center.vue'
    export default {
      props:['initData'],
        data() {
            return {
                placeholder:require('../../assets/img_loading_160x230.png'),
                initScore:require('../../assets/corner_mark.png'),
                HisImg:'HisImg',
                favorite:{
                  assetImg:'http://game-s.yanhuamedia.tv/img/2017/9/11/134/134/20170911155609200edjqqstg.jpeg',
                  score:'6.0',
                  assetName:'微信扫描二维码，报名参加活动'
                },
                isShow:false,
                tittle:{
                    tit:'写字比赛',
                    center:'罗马不是一天就能建成的，而大神也是由萌新一步步成长而来的!虽然说没什么是睡一觉不能解决的，挂个机就能打怪升级，但是要制霸奥罗拉魔法大陆，成为《苍...',
                    butt:'了解比赛详情'
                }
            }
        },
      created(){
//        this.favorite = this.initData.elementList;
//        this.tittle = this.initData.elementList;
        this.eleId = this.initData.specialId;
      },
      methods: {
        //      点击事件
        collectPage(){
          this.traceId = "|"+this.catId+"|"+0;
          console.log(this.traceId+'历史页面');
          // bp.collectPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,asset_id, trace_id, trace_type, special_type, type)
          bi.collectPage(this.traceId,"|1|6|",6,0,1,this.catId,this.assetId,this.traceId,1,0,this.typeid);
        },
        letUp(bl, name, pos,long){
          if (!bl){
            if(pos =="up"){
              this.isShow = true;
              this.addCss();
              this.$emit('eleScroll',this.name,pos,20);
            }
          }
          if(pos =='down'){
              this.$emit('eleScroll',this.name,pos,long);
          }
          if (pos =='KeyEnter'){
            alert(2);
          }
          if(pos == 'back'){
            this.removeCss();
            this.isShow = false;
            this.$emit('pressDown', this.isShow,this.eleId,"back");
          }
        },
        keyCode(params) {
          if (params == "down") {
            this.down();
          }else if (params == "KeyEnter") {
            if (this.num>-1){
              this.isShow = false;
              let url = this.favoriteLists[this.num].list[this.itemNo].layoutUrl;
              let Id =this.favoriteLists[this.num].list[this.itemNo].assetId;
              this.assetId = Id;
              this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'详情',"|1|6|");
              this.typeid = 1;
              console.log("点击时候的typeid"+this.typeid);
              this.collectPage();
            }
          } else if (params == "KeyBack") {
            this.KeyBack();
          } else if (params == "up"){
            this.up();
          }
        },
        KeyBack(){
          this.removeCss();
          this.isShow = false;
          this.$emit('pressDown', this.isShow,this.eleId,"back");
        },
        up(){
          this.removeCss();
          this.$emit('pressDown', this.isShow,this.eleId,"up");
          this.isShow = false;
        },
        down(){
          this.isShow = false;
          this.removeCss();
          this.$refs.interestCenter.updateStast(true, "down");
        },
        addCss(){
          $('.buttons').addClass('change');
        },
        removeCss(){
          $('.buttons').removeClass('change');
        },
        updateStast(bl,pos,isEnd){
          this.isShow = bl;
          this.addCss();
        }
    },
      components: {
        Category_Old_Activity_Center
      }
    }
</script>



