<!--重用历史收藏页-->
<template>
  <div class="box">
    <div class="historyLeft">
      <div class="Back">{{tittle.back}}</div>
      <ul class="Tree">
        <li id="top0">{{tittle.movie}}</li>
        <li id="top1">{{tittle.music}}</li>
        <li id="top2">{{tittle.old}}</li>
        <li id="top3">{{tittle.fun}}</li>
      </ul>
      <div class="bfjl">播放记录</div>
      <div class="wLong">
        <div></div>
      </div>
    </div>
    <historyCenter ref="historyCenter" v-on:pressLeft="letLeft" :json="childJson" :catId="catId" :catCode="catCode"></historyCenter>
    <p class="time"></p>
    <HistoryFirm ref="HistoryFirm" v-on:listernpopup="firm"></HistoryFirm>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="HIstory">
  .bfjl{
    width: 184px;
    height: 65px;
    font-size: 46px;
    color: rgba(255,255,255,0.3);
    position: absolute;
    left: 26px;
    top: 637px;
  }
  .Back.changebox{
    background: #DE6B04;
    border: solid 4px #FFD201;
    box-sizing: border-box;
    line-height: 42px;
  }
  .Tree .changebox{
    background: #DE6B04;
    border: solid 4px #FFD201;
    box-sizing: border-box;
    border-radius: 10px;
    line-height: 32px;
  }
  .wLong{
    width: 2px;
    height: 44px;
    background: #F6F6F7;
    box-shadow: 0 0 1px #F6F6F7;
    border-radius: 1px 1px 1px 1px;
    position: absolute;
    left: 238px;
    top: 165px;
  }
  .wLong div{
    width:0;
    height:0;
    border:5px solid red;
    border-color:transparent #F6F6F7 transparent transparent;
    margin-left: -8px;
    margin-top: 16px;
  }
  .Tree li.change{
    text-shadow:0px 0px 33px #ffffff;
    color: #ffffff;
  }
  .Tree li{
    width: 100px;
    height: 40px;
    font-size: 22px;
    margin-top: 93px;
    color: rgba(255,255,255,0.7);
    text-align: center;
  }
  .Tree{
    margin-left: 20px;
  }
  .Back{
    text-align: center;
    width: 137px;
    height: 50px;
    border-radius: 20px;
    background: rgba(255,255,255,0.2);
    font-size: 22px;
    color: rgba(255,255,255,0.8);
    line-height: 50px;
  }
  .historyLeft{
    width: 150px;
    height: 692px;
    background: #364359;
    padding-left: 90px;
    padding-top: 28px;
  }
  .time{
    font-size: 20px;
    float: right;
    margin-right: 60px;
    line-height: 60px;
    position: absolute;
    right: 0px;
    top: 0px;
    color: rgba(255,255,255,0.7);
  }
  .box{
    background: #1B3247;
    position: relative;
    width: 1280px;
    height: 720px;
    box-sizing: border-box;
    overflow: hidden;
  }
  .box:after{
    clear: both;
    content: '';
    display: block;
  }
  .cs{
    color: black;
    width: 200px;
    height: 300px;
    float: left;
  }
  #divcs{
    background: yellow;
  }
</style>
<script>
  import historyCenter from '../../page/history/historyCenter.vue'
  import HistoryFirm from '../../page/history/historyFirm.vue'
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  export default {
    props:['type','catId','catCode'],
    data() {
      return {
        tittle:{back:"返回上一页",movie:'大片热剧',music:'曲艺荟萃',old:'老年大学',fun:'兴趣社区'},
        currentView: '',
        currentC: 0,
        traceType:'',
        pageType:0,
        oldTime:0,
        focusId:'',
        childJson:[],
        num:0,
        isShow:true,
        movie:[],
        Old:[],
        music:[],
        Fun:[],
        JSON:[],
//        navpos:3
      }
    },
    mounted(){
      this.$nextTick(function() {
        this.$lazyImages.loadImage();
      });
      let num =this.navpos;
      console.log(this.navpos);
      if(num==1){
        this.num=0;
      }
      if(num==2){
        this.num=2;
      }
      if(num==3){
        this.num=1;
      }
      if(num==4){
        this.num=3;
      }
      if(this.num==0){
        this.childJson =this.movie;
      }
      if(this.num==1){
        this.childJson =this.music;
      }
      if(this.num==2){
        this.childJson =this.Old;
      }
      if(this.num==3){
        this.childJson =this.Fun;
      }
      $('#top'+this.num).addClass('changebox');
      $('.wLong').animate({'top': '+='+(135*this.num)},10);
    },
    updated(){
      this.JSON = JSON.parse(yh.queryHistory());
      this.JSON.reverse();
//            this.JSON = [
//
//
//        {"assetId":"1188","assetImg":"http://aged-s.yanhuamedia.tv//img/201610/18/14/56/201610181456585011a555475.jpg","assetName":"勇敢传说","assetType":"Movie","broadcast":"","catId":"4","episode":"1","episodes":"1","id":1,"itemId":"24077","itemImg":"http://aged-s.yanhuamedia.tv//img/201612/17/01/03/20161217010357512645077f2.jpg","itemName":"勇敢传说_国语版","layout":"Detail_Movie","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=1188&c=6","movieLength":"5383840","playurl":"http://vcdn.yanhuamedia.tv/vod/201608/02/16/35/201608021635092423je9qwvq.ts","recordTime":1521022241737,"score":"7.4","specialId":"","watchTime":"1189605"},
//
//
//
//        {"assetId":"201666","assetImg":"http://aged-s.yanhuamedia.tv//img/201803/08/16/39/0/201803081639006017520f677.jpg","assetName":"亮剑","assetType":"Series","broadcast":"","catId":"1","episode":"21","episodes":"30","id":2,"itemId":"254399","itemImg":"","itemName":"亮剑第1集","layout":"Detail_Series","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=201666&c=6","movieLength":"2614560","playurl":"http://vcdn.yanhuamedia.tv/vod/201801/24/22/03/201801242203481894299ad01.ts","recordTime":1521022550756,"score":"9.0","specialId":"","watchTime":"32100"}
//
//
//      ];
      this.getquery();
    },


    created() {
      this.JSON = JSON.parse(yh.queryHistory());
      this.JSON.reverse();
//            this.JSON = [
//
//
//        {"assetId":"1188","assetImg":"http://aged-s.yanhuamedia.tv//img/201610/18/14/56/201610181456585011a555475.jpg","assetName":"勇敢传说","assetType":"Movie","broadcast":"","catId":"4","episode":"1","episodes":"1","id":1,"itemId":"24077","itemImg":"http://aged-s.yanhuamedia.tv//img/201612/17/01/03/20161217010357512645077f2.jpg","itemName":"勇敢传说_国语版","layout":"Detail_Movie","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=1188&c=6","movieLength":"5383840","playurl":"http://vcdn.yanhuamedia.tv/vod/201608/02/16/35/201608021635092423je9qwvq.ts","recordTime":1521022241737,"score":"7.4","specialId":"","watchTime":"1189605"},
//
//
//
//        {"assetId":"201666","assetImg":"http://aged-s.yanhuamedia.tv//img/201803/08/16/39/0/201803081639006017520f677.jpg","assetName":"亮剑","assetType":"Series","broadcast":"","catId":"1","episode":"21","episodes":"30","id":2,"itemId":"254399","itemImg":"","itemName":"亮剑第1集","layout":"Detail_Series","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=201666&c=6","movieLength":"2614560","playurl":"http://vcdn.yanhuamedia.tv/vod/201801/24/22/03/201801242203481894299ad01.ts","recordTime":1521022550756,"score":"9.0","specialId":"","watchTime":"32100"}
//
//
//      ];
      this.getquery();
      this.ds();
      this.load();
    },
    methods:{
      keyCode(params) {
        // console.log("父组件接收子组件触发事件传上来的值");
        if (params == "down") {
          this.down();
        } else if (params == "up") {
          this.up();
        } else if (params == "left") {
          this.left();
        } else if (params == "right") {
          this.right();
        } else if (params == "KeyEnter") {
          if(this.num == -1){
            if(this.$route.query.jsonUrl!=undefined){
              yh.back();
            }else{
              this.$router.go(-1);
            }
          }else return
        } else if (params == "KeyBack") {
          if(this.$route.query.jsonUrl!=undefined){
            yh.back();
          }else{
            this.$router.go(-1);
          }
        }
      },
      right(){
        this.isShow = false;
        this.$refs.historyCenter.updateStast(true, "right");
        $('#top'+this.num).addClass('change');
        $('#top'+this.num).removeClass('changebox');
        $('.Back').removeClass('changebox');
      },
      left(){

      },
      down(){
        $('.Back').removeClass('changebox');
        if(this.num<3){
          $('#top'+this.num).removeClass('changebox');
          this.num++;
          $('#top'+this.num).addClass('changebox');
          if(this.num>0){
            $('.wLong').animate({'top': '+='+135},10);
          }
        }else {
          this.num = 3;
        }
        this.getquery();
        if(this.num==0){
          this.childJson =this.movie;
        }
        if(this.num==1){
          this.childJson =this.music;
        }
        if(this.num==2){
          this.childJson =this.Old;
        }
        if(this.num==3){
          this.childJson =this.Fun;
        }
      },
      up(){
        $('#top'+this.num).removeClass('changebox');
        if(this.num>0){
          this.num--;
          $('.wLong').animate({'top': '-='+135},10);
          $('#top'+this.num).addClass('changebox');
        }else {
          $('.Back').addClass('changebox');
          this.num = -1;
        }
        this.getquery();
        if(this.num==0){
          this.childJson =this.movie;
        }
        if(this.num==1){
          this.childJson =this.music;
        }
        if(this.num==2){
          this.childJson =this.Old;
        }
        if(this.num==3){
          this.childJson =this.Fun;
        }
      },
      load(){
        this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
        this.traceId = "|"+this.catId+"|"+'0';
        this.traceType = "|1|"+this.catCode+"|";
//        this.pageType = 4;
        this.oldTime =performance.timing.domContentLoadedEventEnd-performance.timing.navigationStart;
        console.log(this.oldTime+'=====页面加载时间');
        //      bp.loadPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,special_type,oldTime)
        bi.loadPage(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',this.oldTime);
        console.log(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',this.oldTime+"全部加载信息");
      },
      letLeft(bl, name, pos,url,Id,lx,type,urls){
        if (!bl){
          if(pos =="left"){
            this.isShow = true;
            if(this.num == -1){
              $('.Back').addClass('changebox');
            }else {
              $('#top'+this.num).addClass('changebox');
              $('#top'+this.num).removeClass('change');
            }
          }
          if (pos =='KeyEnter'){
            this.$refs.HistoryFirm.updateStast(true, url,Id,lx,name,type,urls);
          }
        }
      },
        getquery(){
          let newMovie = [];
          let newOld = [];
          let newMusic = [];
          let newFun = [];
          let  fList = this.JSON;
          console.log(this.JSON+'获取到的数据');
//          for(let i = 0;i < fList.length; i++){
//            if(fList[i].layout=="Detail_Movie" || fList[i].layout=="Detail_Series"){
//              newMovie.push(fList[i]);
//            }
//            if(fList[i].layout=="Category_Pic6_List"){
//              newOld.push(fList[i]);
//            }
//            if(fList[i].layout=="Detail_News"){
//              newMusic.push(fList[i]);
//            }
//            if(fList[i].layout=="Category_Pic7_1_Top"){
//              newFun.push(fList[i]);
//            }
//          }
//          换判断catid
          for(let i = 0;i < fList.length; i++){
            if(fList[i].catId=="1"){
              newMovie.push(fList[i]);
            }
            if(fList[i].catId=="2"){
              newOld.push(fList[i]);
            }
            if(fList[i].catId=="3"){
              newMusic.push(fList[i]);
            }
            if(fList[i].catId=="4"){
              newFun.push(fList[i]);
            }
          }
          this.movie = newMovie;
          this.Old = newOld;
          this.music = newMusic;
          this.Fun = newFun;
        },
      ds() {
        setInterval(function () {
          var date = new Date();
          var h = date.getHours(); //获取小时
          var m = date.getMinutes(); //获取分钟
          if (h<10){
            $('.time').text('0' +h +':'+  m );
          }else {
            $('.time').text(h +':'+  m );
          }
          if (m<10){
            $('.time').text(h + ':0' + m );
          }else {
            $('.time').text(h + ':' + m );
          }
        }, 1000)
      },
      firm(bl,name,pos){
        if (!bl){
            this.JSON = JSON.parse(yh.queryHistory());
          this.JSON.reverse();

//          this.JSON = [
//
//
//            {"assetId":"1188","assetImg":"http://aged-s.yanhuamedia.tv//img/201610/18/14/56/201610181456585011a555475.jpg","assetName":"勇敢传说","assetType":"Movie","broadcast":"","catId":"4","episode":"1","episodes":"1","id":1,"itemId":"24077","itemImg":"http://aged-s.yanhuamedia.tv//img/201612/17/01/03/20161217010357512645077f2.jpg","itemName":"勇敢传说_国语版","layout":"Detail_Movie","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=1188&c=6","movieLength":"5383840","playurl":"http://vcdn.yanhuamedia.tv/vod/201608/02/16/35/201608021635092423je9qwvq.ts","recordTime":1521022241737,"score":"7.4","specialId":"","watchTime":"1189605"},
//
//
//
//            {"assetId":"201666","assetImg":"http://aged-s.yanhuamedia.tv//img/201803/08/16/39/0/201803081639006017520f677.jpg","assetName":"亮剑","assetType":"Series","broadcast":"","catId":"1","episode":"1","episodes":"30","id":2,"itemId":"254399","itemImg":"","itemName":"亮剑第1集","layout":"Detail_Series","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=201666&c=6","movieLength":"2614560","playurl":"http://vcdn.yanhuamedia.tv/vod/201801/24/22/03/201801242203481894299ad01.ts","recordTime":1521022550756,"score":"9.0","specialId":"","watchTime":"32100"}
//
//
//          ];
            this.getquery();
            if(this.num==0){
              this.childJson =this.movie;
            }
            if(this.num==1){
              this.childJson =this.music;
            }
            if(this.num==2){
              this.childJson =this.Old;
            }
            if(this.num==3){
              this.childJson =this.Fun;
            }
            this.$refs.historyCenter.updateStast(true,"remove",pos);
          }
        },
      },
    computed: {
      ...mapGetters([
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
        'navpos'
      ])
    },
    components: {
      historyCenter,HistoryFirm
    },
    beforeRouteLeave(to, from, next){
      try {
        StartTime = new Date().getTime();
        console.log("开始时间被重置"+StartTime);
        let overTime = 0;
        this.traceId = "|"+this.catId+"|"+0;
        this.traceType = "|1|"+this.catCode+"|";
        overTime =  new Date().getTime() - this.httpResponseTime;
        bi.closPage(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',overTime);
        console.log(this.traceId ,this.traceType,this.catCode,0,1,this.traceId ,'',overTime);
        console.log("页面停留时间："+overTime);
      } catch (error) {
        console.log(error);
      }finally{
        next();
      }
    }
  }
</script>



