<template>
  <div class="xqsq">
      <ul>
          <li v-for="(item,index) in toper" v-if="index<7"  :key="index" :id="('pic7_'+eleId+'_'+index)"   >
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                  />
          </li>
          <li>
              <div class="listword">
                  <ul>
                      <li v-for="(item,index) in historyList" v-if="index<2" :key="index+7" >
                        <div class='s-title'>
                            <p class="s-name">{{item.itemName}}</p><p class='s-name-temp'></p>
                        </div>
                      </li>
                  </ul>
                  <div class="x_btn">兴趣社 播放记录</div>
              </div>
          </li>
      </ul>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style>
    .xqsq{position: relative;width: 1160px;height:346px; }
    .xqsq li{width: 255px;height: 133px;float: left;margin-right:33px;margin-bottom:33px;}
    .xqsq li div{border:1px solid #95989A;border-radius: 19px;}
    .xqsq li img{width: 100%;height: 100%;border-radius:18px;}
    .listword{width: 255px;height: 80px;border-radius:16px;border: none !important;border:6px solid #FFD800;}
    .listword ul{background-color:rgba(255, 255, 255, 0.14);width: 100%;height: 100%;border-radius: 16px;}
    .listword li{padding: 0px 10px;;width: 235px;height: 40px;line-height: 35px;font-size:20px ; color: #FFFFFF;margin:0px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;}
    .xqsq li.hover{margin: -6px 27px 27px -6px;border-radius:24px;border:6px solid #FFD800;}
    .listword li.hover{background:linear-gradient(180deg,rgba(172, 74, 74, 1),rgba(70, 25, 25, 1));border-radius: 20px;border:1px solid #8F3C3C;margin:-1px;}
    .xqsq .s-title{position: relative;text-align:center;margin-top: 2px;color: #FFFFFF;width:235px;border:none; }
    .s-title{overflow: hidden;text-overflow:ellipsis;white-space: nowrap;text-align:center;}
    .s-name,.s-name-temp{position: relative;display: inline;}
    .s-name-temp{padding-left:25px;}
    .x_btn{position: relative;margin-top: 10px;font-size:20px ; color: #FFFFFF;text-align: right;padding: 0px 15px;line-height:42px;border-radius: 16px;border:none !important;width: 88%;height: 45px;background-color:rgba(255, 255, 255, 0.14);}
    .x_btn.hover{background:linear-gradient(180deg,rgba(172, 74, 74, 1),rgba(70, 25, 25, 1));border-radius: 20px;border:1px solid #8F3C3C;}
</style>
<script>
import {bp as bi} from '../../js/ga.js'
import {mapGetters} from 'vuex'
import {client as yh} from '../../js/client.js'
import c from '../../js/common.js'

export default {
   props:['initData','catId'],
   name:"CategoryXqsq",
   data(){
      return{
          eleId:'',
          toper:[],
          placeholder:require('../../assets/img_loading_352x190.png'),
          initScore:require('../../assets/corner_mark.png'),
          pos:0,
          itemNo:0,
          isShow:false,
          timer:0,
          historyList:[],
      }
  },created(){
      this.toper = this.initData.elementList;
      this.eleId = this.initData.specialId;
      this.initHistory();
  },activated(){
      this.initHistory();
  },mounted(){

  },methods:{
      keyCode(kc) {
        if (kc == "KeyBack") {
            this.back();
        }else if(kc == "right"){
                    this.right();
        }else if(kc == "left"){
                    this.left();
        }else if(kc == "down"){
                    this.down();
        }else if(kc == "up"){
                    this.up();
        }else if(kc == "KeyEnter"){
            try {
                let traceId = "|"+this.catId+"|"+this.toper[this.itemNo].elementId;
                let traceType = "|1|";
              bi.categoryClick(this.catId, this.toper[this.itemNo].elementId, 3, 1, this.catId, traceId, traceType);
            } catch (error) {
                console.log(error);
            }
            if(this.pos==0){
                let url = this.toper[this.itemNo].jsonUrl;
                this.$router.push({name:'Special',params:{jsonUrl:url,catCode:1,catId:202642}});
            }else{
                if(this.itemNo==2){
                    this.$router.push({name:"history",params:{catCode:1,catId:202642}});
                }else{
                     let record = this.historyList[this.itemNo];
                     let jsonStr =  JSON.stringify({catId:record.catId,index:record.episode,startTime:record.watchTime,jsonUrl:record.layoutUrl});
                     yh.play(jsonStr);
                }
            }
        }
    },back() {
        this.removeCss();
        this.isShow = false;
        this.itemNo = 0;
        this.pos =0;
        this.$emit('pressDown', this.isShow,this.eleId,"back");
    },
    right(){
        this.removeCss();
        if(this.itemNo==6){
            this.pos=1;
            this.itemNo=2;
        }else{
            this.itemNo++;
        }
        this.addCss();
    },left(){
        if(this.pos==0&&this.itemNo==0)return;
        this.removeCss();
        if(this.pos==1){
            this.pos=0;
            this.itemNo=6;
        }else{
            this.itemNo--;
        }
        this.addCss();
    },down(){
        this.removeCss();
        if(this.pos==0){
            if(this.itemNo<4){
                if(this.itemNo==3){
                    if(this.historyList.length==0){
                        this.pos=1;
                        this.itemNo=2;
                    }else{
                        this.pos=1;
                        this.itemNo=0;
                    }
                }else{
                    this.itemNo+=4;
                }
            }else{
                this.isShow = false;
                this.$emit('pressDown', this.isShow,this.eleId,"down");
                return;
            }
        }else if(this.pos==1){
            if(this.itemNo<2){
                if(this.historyList.length>=2){
                    this.itemNo++;
                }else{
                    this.itemNo=2;
                }
            }else{
                this.isShow = false;
                this.$emit('pressDown', this.isShow,this.eleId,"down");
                return;
            }
        }
        this.addCss();
    },up(){
        this.removeCss();
        if(this.pos==0){
            if(this.itemNo>3){
                this.itemNo-=4;
            }else{
                this.isShow = false;
                this.$emit('pressDown', this.isShow,this.eleId,"up");
                return;
            }
        }else if(this.pos==1){
            if(this.itemNo>0){
                if(this.historyList.length>=2){
                    this.itemNo--;
                }else if(this.historyList.length==1){
                   this.itemNo=0;
                }
            }else{
                this.pos=0;
                this.itemNo=3;
            }
        }
        this.addCss();
    },addCss(){
        if(this.pos==0){
             $('#pic7_'+this.eleId+'_'+this.itemNo).addClass('hover');
        }else if(this.pos==1){
            if(this.itemNo<2){


                let fs =  $('.listword li').eq(this.itemNo).text().length;
                if(fs>11){

                        let id = '.listword li';
                        let speed = 40;
                        let that = this;
                        let tab =   document.querySelectorAll(id+' .s-title')[this.itemNo];
                        let tab1 =  document.querySelectorAll(id+' .s-name')[this.itemNo];
                        let tab2 =  document.querySelectorAll(id+' .s-name-temp')[this.itemNo];
                        tab.style.textOverflow = 'clip';
                        tab2.innerHTML = tab1.innerHTML;
                        this.timer = setInterval(function(){
                        that.Marquee(tab,tab1,tab2)
                        },speed);
                    };
                $('.listword li').eq(this.itemNo).addClass('hover');
            }else{
                $('.x_btn').addClass('hover');
            }
        }
    },removeCss(){
        if(this.pos==0){
             $('#pic7_'+this.eleId+'_'+this.itemNo).removeClass('hover');
        }else if(this.pos==1){
            if(this.itemNo<2){
                $('.listword li').eq(this.itemNo).removeClass('hover');
                if(this.timer!=0){
                    window.clearInterval(this.timer);
                    let tab =  document.querySelectorAll('.listword li .s-title')[this.itemNo];
                    if(tab!=undefined){
                        tab.scrollLeft = 0;
                        tab.style.textOverflow = 'ellipsis';
                    }
                }
            }else{
                $('.x_btn').removeClass('hover');
            }
        }
    },
    updateStast(bl,pos,isEnd){
        this.isShow = bl;
        this.addCss();
        let eleH = this.cEleOffsetTop('#pic7_'+this.eleId+'_'+this.itemNo);
        this.$emit('eleScroll',this.name,pos,eleH);
    },cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset().top;
        let realHeight = fEleDistance+133;
        console.log("距离顶部距离："+realHeight);
        return realHeight;
    },Marquee(tab,tab1,tab2){
              if(tab2.offsetWidth - tab.scrollLeft <=0){
                  // tab.scrollLeft -= tab1.offsetWidth;
                  tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
              }else{
                  tab.scrollLeft++;
              }
    },initHistory(){
        // 获取点播历史信息
       let arr =  yh.filterHistory(this.navpos);
       this.historyList = arr.reverse();
    },formatDate(mss) {
         return c.formatDate(mss);
     }
  },watch:{
        initData(){
             this.toper = this.initData.elementList;
             this.eleId = this.initData.specialId;
             this.initHistory();
        }
 },computed: {
      ...mapGetters([
        'category',
        'behindParams',
        'isHTTPS',
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
        'navpos'
      ])
    }
}
</script>



