<!-- 二级页面 -->
<template>
  <div class="secondLevelPage">
      <div class="second-top">
          <div class="second-back">返回上一页</div>
          <div class="second-title">{{category[navpos].catName}}-{{categoryName}}</div>
      </div>
      <div class="secondList">
          <ul class="scrollbar">
              <li v-for="(item,index) in elementList"  :key="index" >
                    <lazy-image
                                :src='item.elementImg||item.assetImg'
                                :placeholder='placeholder'
                                :initScore = 'initScore'
                                :score = "item.score"
                                :imgClass = "round"
                    />
                    <div class='s-title'>
                        <p class="s-name">{{item.assetName}}</p><p class='s-name-temp'></p>
                    </div>
              </li>
          </ul>
      </div>
      <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
    .secondLevelPage{position:relative;width:1210px;padding-left:70px;}
    .second-top{position:relative;width: 1200px;height: 50px;margin:28px 0px 0px 15px;}
    .second-back{position:relative;width: 137px;height: 50px;display: inline-block;border-radius: 25px;background-color:rgba(255, 255, 255, 0.18);color:rgba(255, 255, 255, 0.7);font-size:20px;font-weight: 500;letter-spacing:3px;line-height:50px;float: left;}
    .second-title{position:relative;width: 283px;height: 45px;left:310px;color:rgba(255, 255, 255, 0.71);opacity:0.32;font-size: 32px;display: inline-block; }
    .secondList{position: relative;overflow: hidden;width: 1200px;height: 642px;}
    .scrollbar{position: relative;height:100%;width:100%;margin-top: 27px;margin-left: 10px;}
    .secondList li{float:left;margin:0px 31px 65px 0px;width: 160px;height: 230px;}
    .s-title{overflow: hidden;text-overflow:ellipsis;white-space: nowrap;text-align:center;margin-top: 8px;color: #FFFFFF;width: 160px;}
    .secondList li .s-name,.secondList li .s-name-temp{position: relative;font-size:20px;letter-spacing:2px;color: #FFFFFF;}
    .s-name,.s-name-temp{display:inline;}
    .s-name-temp{padding-left:25px;}
    .secondList li .imglazy{width: 100%;height: 100%;border-radius:15px;border: 1px solid #95989A;}
    .secondList li div img{border-radius:15px;}
    .secondList li.hover{border:6px solid #FFD800;margin: -6px 25px 59px -6px;border-radius:22px;z-index:99;}
    .second-back.hover{border:6px solid #FFD800;margin:-6px;}
</style>
<style>
    .round{border-radius:15px;}
</style>
<script>
import {HTTPUtil as api} from '../../fetch/api'
import {mapGetters} from 'vuex'
import {bp as bi}  from '../../js/ga.js'

export default {
  props:['jsonUrl','catCode','categoryName','catId','parentId'],
  name:"secondLevelPage",
  data(){
      return{
          elementList:[],
          placeholder:require('../../assets/img_loading_160x230.png'),
          initScore:require('../../assets/corner_mark.png'),
          isShow:true,
          pos:0,
          itemNo:0,
          timer:0,
          pageNum:0,
          pindex:18,
          psize:12,
          pageSize:0,
          cheight:[],
          round:'round',
          title:''
      }
  },computed: {
      ...mapGetters([
        'category',
        'navpos',
        'httpRequestTime',
        'httpResponseTime',
      ])
    },created(){
     // let curl = "https://winnow-bds.yanhuamedia.tv/?s=14|2&p=yhScreenResult&k=1&v=1&content=1491378371928487839&catCode=movie&pindex=0&psize=18";
      let curl = this.jsonUrl+'&returnType=jsonp&pindex=0&psize=18';
      api.get(curl).then((jsonData)=>{

          if(jsonData.code==200){
              this.elementList = jsonData.data.elementList;
              this.$nextTick(function(){
                  this.$lazyImages.loadImage();
                  this.addCss();
              })
          }
      })
  },mounted(){

  },methods:{
      keyCode(kc) {
                if (kc == "KeyBack") {
                    if(this.itemNo>17){
                        $('.scrollbar').stop(false,true).animate({'top':'0px'},200);
                        this.cheight = [];
                        this.pageSize = 0 ;
                        this.pos = 1;
                        this.itemNo = 0;
                        this.addCss();

                    }else{

                        this.$router.go(-1);
                    }
                }else if(kc == "right"){
                  this.right();
                }else if(kc == "left"){
                  this.left();
                }else if(kc == "down"){
                  if($('.scrollbar').is(":animated")){return}
                  this.down();
                }else if(kc == "up"){
                  if($('.scrollbar').is(":animated")){return}
                  this.up();
                }else if(kc == "KeyEnter"){
                    if(this.pos==1){
                        this.$router.go(-1);
                    }else{
                             let url = this.elementList[this.itemNo].jsonUrl;
                             this.$router.push({name:"Detail",params:{jsonUrl:url,catCode:1,catId:this.catId,parent:1,parentId:this.catId}});
                    }
                }
     },right(){
        if((this.itemNo+1)%6==0)return;
        this.removeCss();
        this.itemNo++;
        this.addCss();
     },left(){
        if(this.itemNo%6==0)return;
        this.removeCss();
        this.itemNo--;
        this.addCss();
     },down(){

        this.removeCss();
        if(this.pos==1){
            this.pos=0;
            this.itemNo=0;
        }else{
            this.itemNo+=6;
        }
        let domHeight = this.cEleOffsetTop('.secondList li:eq('+this.itemNo+')');
        let scrollY = $('.scrollbar').position().top;
        if(domHeight>720){
            let movelength = domHeight - 636;
                 scrollY -= movelength;
                 let _this = this;
                 $('.scrollbar').stop(false,true).animate({'top': scrollY+'px'},350,function(){
                     _this.cheight.push(movelength);
                    _this.pageSize++;
                });
                 this.nextPage();
        }
        this.addCss();
     },up(){
        this.removeCss();
        if(this.pos==0&&this.itemNo<6){
            this.pos = 1;
        }else{
            this.itemNo-=6;
        }
        let domHeight = this.cEleOffsetTop('.secondList li:eq('+this.itemNo+')');
        let scrollY = $('.scrollbar').position().top;
        if(domHeight<400&&this.pageSize>0){
                 if(this.pageSize==0){
                 $('.scrollbar').stop(false,true).animate({'top':'0px'},200);
                    return
                };
                scrollY += this.cheight[this.pageSize-1];
                console.log(scrollY);
                let _this = this;
                $('.scrollbar').stop(true,true).animate({'top':scrollY+'px'},100,function(){
                        _this.cheight.splice((_this.pageSize-1),1);
                        _this.pageSize--;
                });
        }
        this.addCss();
     },addCss(){
         if(this.pos==0){
             $('.secondList li').eq(this.itemNo).addClass('hover');
             let p_title = $('.secondList li .s-name').eq(this.itemNo).text();
             if(p_title.length>=7){
                let id = '.secondList li';
                let speed = 40;
                let that = this;
                let tab =   document.querySelectorAll(id+' .s-title')[this.itemNo];
                let tab1 =  document.querySelectorAll(id+' .s-name')[this.itemNo];
                let tab2 =  document.querySelectorAll(id+' .s-name-temp')[this.itemNo];
                tab.style.textOverflow = 'clip';
                tab2.innerHTML = tab1.innerHTML;
                this.timer = setInterval(function(){
                  that.Marquee(tab,tab1,tab2)
                },speed);
             }

     }else if(this.pos==1){
             $('.second-back').addClass('hover');
         }
     },removeCss(){
          if(this.pos==0){
             $('.secondList li').eq(this.itemNo).removeClass('hover');
              if(this.timer!=0){
                clearInterval(this.timer);
                let tab =  document.querySelectorAll('.secondList li .s-title')[this.itemNo];;
                if(tab!=undefined){
                    tab.scrollLeft = 0;
                    tab.style.textOverflow = 'ellipsis';
                }
            }
         }else if(this.pos==1){
             $('.second-back').removeClass('hover');
         }
     },Marquee(tab,tab1,tab2){
              if(tab2.offsetWidth - tab.scrollLeft <=0){
                  // tab.scrollLeft -= tab1.offsetWidth;
                  tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
              }else{
                  tab.scrollLeft++;
              }
    },nextPage(){

        this.pindex = this.pindex+12;
        let curl = "https://winnow-bds.yanhuamedia.tv/?s=14|2&p=yhScreenResult&k=1&v=1&returnType=jsonp&content=1491378371928487839&catCode=movie&pindex="+this.pindex+"&psize="+this.psize;
        api.get(curl).then((jsonData)=>{
          console.log(jsonData);
          if(jsonData.code==200){
              let temp = jsonData.data.assetList;
              this.elementList = this.elementList.concat(temp);
          }
        }).then(()=>{
            this.$lazyImages.loadImage();
        })
    },cEleOffsetTop(ele){
            let fEleDistance = $(ele).offset().top;
             console.log(ele,"元素距离文档顶部的距离:"+(fEleDistance+242));
            let realHeight = fEleDistance+242;
            return realHeight;
    },
  },beforeRouteLeave(to, from, next){
      try {
           this.$store.commit('GET_STARTTIME',new Date().getTime());
           console.log("开始时间被重置"+this.startTime);
           let overTime = 0;
           overTime =  new Date().getTime() - this.httpResponseTime;
           bi.closPage(this.focusId,1,1,this.focusId,0,0,0,overTime)
           console.log("页面停留时间："+overTime+"^^^^^^^focusId:"+this.focusId);
      } catch (error) {
           console.log(error);
      }finally{
           next();
      }
    }
}
</script>



