import fetchJsonp from 'fetch-jsonp';

/**
 * 基于fetch封装get jsonp请求请求
 *
 */

var HTTPUtil = {};


HTTPUtil.get =  function (url) {
  console.log("url:" + url);
  if (url.indexOf('localhost')>0){
    return new Promise(function (resolve, reject) {
      fetch(url).then(function (response) {
          if (response.ok) {
            return response.json();
          } else {
            reject({ status: response.status });
          }
        })
        .then(function (response) {
          resolve(response);
        })
        .catch(function (ex) {
          console.log(ex);
          reject({ status: -1 });
        })
    })
  }else{
    return new Promise(function (resolve, reject) {
      fetchJsonp(url, {
        jsonpCallback: 'jsonpCallback',
      })
        .then(function (response) {
          if (response.ok) {
            return response.json();
          } else {
            reject({ status: response.status });
          }
        })
        .then(function (response) {
          resolve(response);
        })
        .catch(function (ex) {
          console.log(ex);
          reject({ status: -1 });
        })
    })
  }

};



// 获取主题接口
HTTPUtil.getTheme = function(url) {
  return  HTTPUtil.get(url);
}

// 获取栏目筛选条件
HTTPUtil.getFilter = function(catCode){
  // let url = "https://winnow-bds.yanhuamedia.tv/?s=17|6&p=yhCategoryList&k=1&v=1&catCode=" + catCode;
  let url = "https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhCategoryList&k=1&v=1&catCode=" + catCode;

  return url;
}

// 获取栏目筛选结果
HTTPUtil.getFilterResult = function (condition, index, pageSize,catCode) {
  // let url = "https://winnow-bds.yanhuamedia.tv/?s=17|6&p=yhScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&catCode=" + catCode;
  let url = "https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhScreenResult&k=1&v=1&returnType=jsonp" + condition + "&pindex=" + index + "&psize=" + pageSize + "&catCode=" + catCode;
  
  return HTTPUtil.get(url);
}

// bi数据埋点请求发送
HTTPUtil.biHttp = function(url){
    console.log("bi数据埋点请求:" + url);
    fetchJsonp(url, {
        jsonpCallback: 'jsonpCallback',
    }).catch(function(){
      console.log("Bi success");
    })
}

export  {HTTPUtil};



