import Vue from 'vue'
import Router from 'vue-router'
import Category from '@/components/Category'
import TemplateRendering from '../page/module/Template_Rendering.vue'
import DetailTop from '../page/detail/DetailTop.vue'
import Detail from '../page/detail/Detail.vue'
import SecondLevelPage from '../page/module/Second_Level_Page.vue'
import DetailList from '../page/detail/DetailList.vue'
import DetailListOne from '../page/detail/DetailListOne.vue'
import DetailRecom from '../page/detail/DetailRecom.vue'
import Detailfirm from '../page/detail/Detailfirm.vue'
import Special from '../page/special/special.vue'
import history from '../page/history/history.vue'
import YtoneService from '../page/service/ytone/YtoneServices.vue'
import YtoneDetail from '../page/service/ytone/YtoneDetail.vue'
import YtoneOrder from '../page/service/ytone/YtoneOrder.vue'
import CategoryOldActivity from '../page/interest/Category_Old_Activity.vue'



Vue.use(Router);


export default new Router({
  base: '/',
  mode: 'history',
  routes: [
    { path: '/', redirect: '/agedness/index'},
    {
      path: '/agedness', name: 'agedness', component: Category, props: true, meta: { keepAlive: true},
      children: [{ path: 'index', name: 'index', component: TemplateRendering, props: true, meta: { keepAlive: true }}]
    },
    // { path: '/DetailTop',name:'DetailTop',component:DetailTop,props: true, meta: { keepAlive: false}},
    { path: '/agedness/detail',name:'Detail',component:Detail,props: true, meta: { keepAlive: false }},
    // { path: '/DetailList',name:'DetailList',component:DetailList,props: true, meta: { keepAlive: false}},
    // { path: '/DetailListOne',name:'DetailListOne',component:DetailListOne,props: true, meta: { keepAlive: false}},
    // { path: '/DetailRecom',name:'DetailRecom',component:DetailRecom,props: true, meta: { keepAlive: false}},
    // { path: '/Detailfirm',name:'Detailfirm',component:Detailfirm,props: true, meta: { keepAlive: false}},
    { path: '/column', name: 'column', component: SecondLevelPage, props: true, meta: { keepAlive: true }},
    { path: '/hotTopics',name:'Special',component:Special,props: true,meta: { keepAlive: false }},
    { path: '/history',name:'history',component:history,props: true,meta: { keepAlive: false }},
    { path: '/ytone', name: 'ytone', component: YtoneService, props: true, meta: { keepAlive: false }},
    { path: '/ytDetail', name: 'ytDetail', component: YtoneDetail, props: true, meta: { keepAlive: false } },
    { path: '/ytOrder', name: 'ytOrder', component: YtoneOrder, props: true, meta: { keepAlive: false }},
    { path:'/CategoryOldActivity',name:'CategoryOldActivity',component:CategoryOldActivity,props: true,meta: { keepAlive: false }}
  ]
})




