<!--详情页——双层选集层-->
<template>
  <div style="box-sizing: border-box;" class="pt" >
    <div class="ption-top " id="pt2">
      <div class="ption">
        <p v-if="!this.acc" class="text_numx" id="c0">暂无片源</p>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="list">
  .pt{
    width: 1120px;
    height: 90px;
    background: #100B1B;
    padding-top: 27px;
    padding-left: 34px;
    border-radius: 0px 0px 15px 15px;
  }
  .ption-top{
    width: 1040px;
    height: 114px;
    overflow: hidden;
    margin-top: 3px;
    border-radius: 5px;
    position: relative;
  }
  .ption{
    width: 1065px;
    height: 114px;
    position: absolute;
    left: 0;
    top: 4px;
    transition: all 0.01s;
  }
  .ption .text_num{
    width: 120px;
    height: 50px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    margin-right: 5px;
    margin-top: 5px;
    float: left;
    line-height: 50px;
    border-radius: 15px;
    background: rgba(255,255,255,0.2);
    color: white;
    box-sizing: border-box;
  }
  .ption .text_numx{
    width: 120px;
    height: 50px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    margin-right: 5px;
    margin-top: 5px;
    float: left;
    line-height: 50px;
    border-radius: 15px;
    background: rgba(255,255,255,0.2);
    color: white;
    box-sizing: border-box;
  }
  .ption .text_num.backchange{
    background: #F36100;
    border: solid 4px #F8D203;
    line-height: 42px;
  }
  .ption .text_numx.backchange{
    background: #F36100;
    border: solid 4px #F8D203;
    line-height: 42px;
  }
</style>
<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  export default {
    props:['initData','jsonUrl',"catCode","catId","parent","parentId"],
    data() {
      return {
        name:'DetailListOne',
        op:0,
        itemNo: 0,
        num: 0,
        a:0,
        showl:true,
        isShow:true,
        tops:
          [
            {
              episode: "",
            }
          ],
        acc:{
          episodes:''
        }
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },

    mounted() {
      this.tops = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.ceil(this.numm/32);
//      if(this.acc=1){
//        $(".pt").css("height", "120px");
//      }else {
//        $(".pt").css("height", "240px");
//      }
      $('#a'+(this.itemNo)).addClass('backchange');
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        }else if(kc=='KeyEnter'){
          this.KeyEnter();
        }
      },
      down(){
        this.isShow = false;
        this.removeCss();
        this.$emit('pressDown', this.isShow, this.name, "down");
      },
      up(){
        this.isShow = false;
        this.removeCss();
        this.$emit('pressDown', this.isShow, this.name, "up");
      },
      back() {
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      removeCss(){
        $('#c'+(this.num)).removeClass('backchange');
      },
      addCss(){
        $('#c'+(this.num)).addClass('backchange');
      },
      updateStast(bl,pos) {
//        this.isShow = true;
//        this.addCss();
      },
      cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+450;
        return realHeight;
        console.log(fEleDistance+'获取的高度');
      },
      KeyEnter(){
          let tempUrl = this.jsonUrl.replace('https://winnow-bds.yanhuamedia.tv','http://winnow-ds.yanhuamedia.tv');
          let obj = JSON.stringify({index:this.num,startTime:0,jsonUrl:tempUrl});
          let id = this.initData.data.itemList[this.num].itemId;
          console.log(id+'下方推荐');
          let pageId =this.initData.data.assetId;
          let traceId = "|"+this.catId;
          let traceType = "|"+this.catCode;
          //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
          bi.categoryClick(this.catId,id,5,31,pageId,traceId,traceType);
          console.log(this.catId,id,5,31,pageId,traceId,traceType+'点击事件');
          yh.play(obj);
          console.log(obj);
      }
    },
    watch:{
      initData(){
        this.tops = this.initData.data.itemList;
        this.acc = this.initData.data.itemList.length;
        this.numm = parseFloat(this.acc);
        this.a = Math.ceil(this.numm/34);
        this.op = 0;
        this.itemNo = 0;
        this.num = 0;
        $('.ption').animate({'top': '0'},50);
      }
    },
    components: {
    }

  }

</script>



