<template>
  <div class="orderBox">
       <div class="orderCategory">
          <div class="yt-back">返回上一页</div>
          <ul class="serviceName">
              <li class="hover">个人信息<div class="lfocus"><span class="l-arrows"></span></div></li>
              <li>全部订单<div class="lfocus"><span class="l-arrows"></span></div></li>
          </ul>
          <div class="watermark">个人中心</div>
      </div>
      <div class="userDetail">
         <user-info v-if="sw" v-on:menu="menuOption" ></user-info>
         <all-orders v-if="!sw" v-on:menu="menuOption" :iskeyDo="isShow" ></all-orders>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style>
    #app{padding: 0px;width: 1280px;height: 720px;overflow:hidden;}
    .orderCategory,.Retail{display: inline-block}
    .orderCategory{position: relative;width: 235px;height: 720px;float: left;border-radius: 0px 15px 0px 0px;background-color: rgba(53, 66, 96, 1);box-shadow: 0px 3px blur 6px;}
    .Retail{position: relative;width:1045px;height: 720px;}
    .yt-back{position: absolute;top: 21px;left: 81px;width: 137px;height: 50px;line-height:50px;border-radius: 25px;background-color: rgba(255, 255, 255, 0.18);color: rgba(255, 255, 255, 0.7);font-size: 22px;letter-spacing: 1px;}
    .watermark{position: absolute;top: 636px;left: 26px;width: 184px;height: 65px;font-size:46px;color: rgba(255, 255, 255, 0.35);opacity: 0.3888;}
    .serviceName{position: absolute;display: flex;display: -webkit-flex;flex-direction:column;justify-content:center; width: 120px;height: 500px;left: 95px;top:105px;font-size:25px;color: #FFFFFF;text-align: center;}
    .serviceName li{margin-bottom:62px;opacity: 0.7372;letter-spacing: 1px;padding: 3px;height: 36px;}
    .serviceName li.color{color: #FFFFFF;text-shadow: 0px 3px 20px rgba(255,255,255,1);opacity: 1;}
    .serviceName li.hover{background-color:#C97400;border:6px solid #FFD800;color: #FFFFFF;opacity: 1;border-radius:15px;margin: -6px -6px 56px -6px;  }
    .lfocus{position: absolute;width: 3px;height: 45px;border-right: #FFFFFF 3px solid;right: 0px;margin-top: -37px;margin-right: -14px;border-radius: 10px 0px 0px 10px;display: none;}
    .l-arrows{display: block;width: 0;height: 0;border-width: 5px 5px 5px 0;border-style: solid;border-color: transparent rgba(255, 255, 255, 1) transparent transparent;position: absolute;top: 16px;left: -2px;}
    .userDetail{position: relative;float: left;width: 1045px;}
    .yt-back.hover{border:6px solid #FFD800;margin: -6px;}

</style>
<script>
import UserInfo from '../ytone/UserInfo'
import AllOrders from '../ytone/AllOrders'
import {ytone} from '../../../js/ytone.js'
import c from '../../../js/common.js'
//  window.loginCallback = function(info){
//     console.log("YT return："+JSON.stringify(info));
// };

export default {
  data(){
      return{
          isShow:true,
          pos:0,
          itemNo:0,
          sw:true,
          vcode:'',
      }
  },components:{
      UserInfo,AllOrders
  },created(){
     this.vcode = c.getLocal('yt-vcode');
     ytone.getServerTime(ytone.keepTime,'getOrderListTime');
  },mounted(){

  },methods:{
     keyCode(kc) {
        if (kc == "KeyBack") {
            this.$router.go(-1);
        }else if(kc == "right"){
                    this.right();
        }else if(kc == "left"){
                    this.left();
        }else if(kc == "down"){
                    this.down();
        }else if(kc == "up"){
                    this.up();
        }else if(kc == "KeyEnter"){
            if(this.pos==1){
              this.$router.go(-1);
            }
        }
      },up(){
           if(this.pos==0){
              this.removeCss();
              if(this.itemNo==0){
                this.pos=1
              }else{
                this.itemNo--;
              }
              this.addCss();
            }
      },down(){
           if(this.pos==0){
               if(this.itemNo==1)return;
              this.removeCss();
              this.itemNo++;
              this.addCss();
          }else if(this.pos==1){
              this.removeCss();
              this.pos=0;
              this.addCss();
          }
      },left(){

      },right(){
          if(this.pos==0){
              if(this.itemNo==0)return;
              this.removeCss();
              $('.lfocus').eq(this.itemNo).show();
              $('.serviceName li').eq(this.itemNo).addClass('color');
              this.isShow = false;
          }
      },addCss(){
          if(this.pos==0){
              if(this.itemNo==0){
                  this.sw=true;
              }else{
                  this.sw=false;
              }
              $('.serviceName li').eq(this.itemNo).addClass('hover');
          }else if(this.pos==1){
              $('.yt-back').addClass('hover');
          }
      },removeCss(){
          if(this.pos==0){
              $('.serviceName li').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==1){
              $('.yt-back').removeClass('hover');
          }
      },menuOption(keyState,id,pos){
          $('.lfocus').eq(this.itemNo).hide();
          $('.serviceName li').eq(this.itemNo).removeClass('color');
          this.isShow = !keyState;
          this.addCss();
      },getOrderListTime(ob){
          if(ob.code==1000){
              const timespace = ob.no;
              const signature = ytone.md5(ytone.account + 20 + 1 + timespace + msg + this.vcode+ ytone.Key);
              let jsonOb = '{\"signature\":\"'+signature+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"currpage\":1,\"callType\":11,\"facilitator_id\":\"'+this.facilitator_id+'\",\"service_ext\":\"'+this.detailInfo.memo+'\"}';
          }
      }
  },

}
</script>



