<!--历史详情页-->
<style>
  .HistoryImg{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-sizing: border-box;
  }
</style>
<template>
  <div class="actor-right">
    <div v-for="(item,index) in favoriteLists" v-if="index>=0" :key="item.recordTime" :id="('a'+index)" class="actor_ul">
      <div class="actor_year">
        <div></div>
        <p>{{item.recordTime}}</p>
      </div>
      <div class="actorImgs">
        <div class="actor_img" v-for="(favorite,index) in item.list" :key="index" :id="('b'+index)">
          <!--<img :src="assert.assetImg" >-->
          <lazy-image
            :src='favorite.assetImg'
            :placeholder='placeholder'
            :initScore = 'initScore'
            :score = "favorite.score"
            :title = "favorite.assetName"
            class="imgList_img"
            :imgClass='HistoryImg'
          />
          <div class="actor_name">第{{favorite.episode}}集  {{(favorite.watchTime)}}</div>
        </div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode"  ref="keyDo" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="HisC">
  .actor_name{
    width: 132px;
    height: 28px;
    font-size: 20px;
    color: white;
    text-align: center;
    line-height: 28px;
    margin-top: 5px;
    margin-left: 10px;
  }
  .actorImgs{
    width: 3000px;
    margin-left: 10px;
  }
  .actor_year{
    margin-left: 26px;
  }
  .actor_year:after{
    clear: both;
    display: block;
    content: '';
  }
  .actor_year div{
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(255,255,255,0.7);
    float: left;
    margin-top: 15px;
    margin-right: 10px;
  }
  .actor_year p{
    font-size: 30px;
    text-align: left;
    color: rgba(255,255,255,0.7);
  }
  .actor_ul{
    width: 100%;
    margin-top: 20px;
    overflow: hidden;
    height: 350px;
  }
  .actor_ul:after{
    clear: both;
    content: "";
    display: block;
  }
  .actor_img{
    width: 160px;
    height: 232px;
    margin-top: 40px;
    margin-right: 29px;
    position: relative;
    border: 1px solid rgba(255,255,255,0.5);
    float: left;
    box-sizing: border-box;
    border-radius: 15px;
  }
  .actor_img img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
  }
  .actor-right{
    width: 980px;
    background:#1B3247;
    box-sizing: border-box;
    position: absolute;
    left: 276px;
    top:0;
  }
  .actor_ul .change{
    border: 4px solid #FFD000;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.55);
    box-sizing: border-box;
  }

</style>

<script>
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'

  var d = new Date();
  export default {
    props:['json','catId','catCode'],
    data () {
      return {
        name: "HistoryCenter",
        isShow: false,
        num: 0,
        itemNo: 0,
        sc: false,
        placeholder: require('../../assets/img_loading_160x230.png'),
        initScore: require('../../assets/corner_mark.png'),
        historyList:[],
        favoriteList: [],
        favoriteLists: [],
        HistoryImg:'HistoryImg',
        assetId:0,
        typeId:1,
        traceId:''
      }
    },components:{

    },
    created() {
      this.timeTime();
      this.getquery(this.json);
      this.jsonfun(this.favoriteList)
    },mounted(){
      this.getquery(this.json);
      this.jsonfun(this.favoriteList);
    },watch:{
      json(){
        this.timeTime();
        this.getquery(this.json);
        this.jsonfun(this.favoriteList);
      }
    }
    ,updated(){

    },
    activated(){
      this.timeTime();
      this.getquery(this.json);
      this.jsonfun(this.favoriteList);
    },
    methods: {
      //      点击事件
      collectPage(){
        this.traceId = "|"+this.catId+"|"+0;
        console.log(this.traceId+'历史页面');
//        bp.historyClick = function(asset_id, type, traceId, traceType, pageType, pageId, parent_page_type, parent_page_id)
        // bp.collectPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,asset_id, trace_id, trace_type, special_type, type)
//        bi.collectPage(this.traceId,'|1|6|',6,0,1,this.catId,this.assetId,this.traceId,1,0,this.typeid);
        console.log(this.traceId,'|1|6|',6,0,1,this.catId,this.assetId,this.traceId,1,0,this.typeid);
        bi.historyClick(this.assetId,this.typeid,this.traceId,"|1|6|",6,0,1,this.catId);
      },
      changeJson(jsonDB){
        this.historyList = jsonDB;
        let _tempArray = [];
        let _this = this;//  sort 作用域在windows下面需要注意
        let sortedObjKeys = Object.keys(this.historyList).sort(function(a, b) {
          return _this.historyList[b].recordTime - _this.historyList[a].recordTime;
        });
        for (let index in sortedObjKeys) {
          _tempArray.push(this.historyList[sortedObjKeys[index]]);
        }
        this.historyList  = _tempArray;
        console.log(this.historyList);
      },
      getquery(jsonDB){
        this.changeJson(jsonDB);
        let newFavorite = [];
        let  fList = this.historyList;
        for (let index = 0; index < fList.length-1; index++) {
          if(this.Data(this.json[index].recordTime) == 0) {
            let favorite = {recordTime: "今天", list:[fList[index]]};
            newFavorite.push(favorite);
          }else if (this.Data(this.json[index].recordTime) == 1){
            let favorite = {recordTime: "昨天", list:[fList[index]]};
            newFavorite.push(favorite);
          }else if (this.Data(this.json[index].recordTime) == 2){
            let favorite = {recordTime: "前天", list:[fList[index]]};
            newFavorite.push(favorite);
          }else if (this.Data(this.json[index].recordTime)>2){
            let favorite = {recordTime: "更早时间", list:[fList[index]]};
            newFavorite.push(favorite);
          }
        }
        this.favoriteList = newFavorite;
      },

      jsonfun(jsonDA){
        let newFavorites = [
          {recordTime: "今天", list:[]},
          {recordTime: "昨天", list:[]},
          {recordTime: "前天", list:[]},
          {recordTime: "更早时间", list:[]}];
        let newList = [];
        let fList = jsonDA;
        for (let i = 0;i < fList.length; i++) {
          if (newFavorites.length == 0){
            newFavorites.push(fList[i]);
          }else {
            for(let j = 0;j < newFavorites.length; j++){
              if(fList[i].recordTime == newFavorites[j].recordTime){
                newFavorites[j].list.push(fList[i].list[0]);
              }
            }
          }
        }
        for(let i =0;i < newFavorites.length;i++){
          if (newFavorites[i].list.length !=0){
            console.log(newFavorites[i].list);
            newList.push(newFavorites[i]);
          }
        }
        this.favoriteLists = newList;
      },
      getLocalTime(tm) {
        return new Date(tm).toLocaleString().replace(/\//g, "-");
      },
      keyCode(params) {
        if (params == "down") {
          this.down();
        } else if (params == "up") {
          this.up();
        } else if (params == "left") {
          this.left();
        } else if (params == "right") {
          this.right();
        } else if (params == "KeyEnter") {
          if (this.num>-1){
            this.isShow = false;
            let url = this.favoriteLists[this.num].list[this.itemNo].layoutUrl;
            let urls = this.favoriteLists[this.num].list[this.itemNo];
            console.log(urls+"数据");
            let Id =this.favoriteLists[this.num].list[this.itemNo].assetId;
            this.assetId = Id;
            console.log("专题ID"+this.assetId);
            this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'详情',"|1|6|",urls);
            this.typeid = 1;
            console.log("点击时候的typeid"+this.typeid);
            this.collectPage();
          }
        } else if (params == "KeyBack") {
          this.KeyBack();
        }
      }, KeyBack() {
        this.isShow = false;
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },down() {
        this.removeCss();
        this.num++;
        this.itemNo =0;
        if (this.num>this.favoriteLists.length-1){
          this.num = this.favoriteLists.length-1;
        }
        $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
        $('.actor-right').css({'transition':'all 0.3s','transform':'translateY(-'+ 360*this.num +'px)'});
        this.addCss();
      },
      up(){
        this.removeCss();
        this.num--;
        this.itemNo =0;
        if (this.num<=0){
          this.num= 0;
        }
        $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
        $('.actor-right').css({'transition':'all 0.3s','transform':'translateY(-'+ 360*this.num +'px)'});
        this.addCss();
      },
      left(){
        this.removeCss();
        this.itemNo--;
        if(this.itemNo<0){
          this.itemNo=-1;
          this.num = 0;
          $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
          $('.actor-right').css({'transition':'all 0.1s','transform':'translateY(-'+ 360*this.num +'px)'});
          this.isShow = false;
          this.$emit('pressLeft', this.isShow,this.name,"left");
        }
        if (this.itemNo<this.favoriteLists[this.num].list.length-2){
          $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
        }
        this.addCss();
      },
      right(){
        this.removeCss();
        this.itemNo++;
        if (this.itemNo>this.favoriteLists[this.num].list.length-1){
          this.itemNo = this.favoriteLists[this.num].list.length-1;
        }
        if (this.itemNo>2){
          $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*(this.itemNo-2) +'px)'});
        }
        this.addCss();
      },
      addCss(){
        $('#a'+this.num+' #b'+this.itemNo).addClass('change');
      },
      removeCss(){
        $('#a'+this.num+' #b'+this.itemNo).removeClass('change');
      },
      updateStast(bl,name,pos){
//        线上
//        this.json = JSON.parse(yh.queryHistory());
        this.getquery(this.json);
        this.jsonfun(this.favoriteList);
        this.isShow = bl;
        if(bl){
          this.removeCss();
          this.num = 0;
          this.itemNo = 0;
          this.addCss();
          $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
          $('.actor-right').css({'transition':'all 0.1s','transform':'translateY(-'+ 360*this.num +'px)'});
        }
        if(name == 'remove'){
          this.removeCss();
          this.num = 0;
          this.itemNo = 0;
          this.addCss();
          $('#a'+this.num+' .actorImgs').css({'transition':'all 0.3s','transform':'translateX(-'+ 175*this.itemNo +'px)'});
          $('.actor-right').css({'transition':'all 0.1s','transform':'translateY(-'+ 360*this.num +'px)'});
          if(pos == 'keyEnter'){
            this.typeid = 2;
            console.log("删除历史时候的typeid"+this.typeid);
            console.log(this.assetId,this.typeid,this.traceId,"|1|6|",6,0,1,this.catId);
            this.collectPage();
          }
        }
      },
      Data(gettime){
        let newtime =new Date(new Date().setHours(0,0,0,0));
        var timestamp = Date.parse(newtime);
        let timeout = Math.ceil((timestamp-gettime)/86400000);
        return timeout;
      },
      ToTime(mss) {
        let hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        let minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
        let seconds = parseInt((mss % (1000 * 60)) / 1000);
        return hours + ":" + minutes + ":" + seconds;
      },
      timeTime(){
        let Day = this.json;
        for(let i=0;i<Day.length;i++){
          console.log('1');
          if(Day[i].watchTime.indexOf(":")<0){
            console.log('2');
            Day[i].watchTime = this.ToTime(Day[i].watchTime);
            console.log(Day[i].watchTime+'我们需要的时间');
          }
        }
//        console.log(JSON.parse(Day)+'最终数据');
        this.json = Day;
      }
    },computed:{
      ...mapGetters([
        'rootPath',
        'catList',
        'category',
        'behindParams',
        'isHTTPS'
      ])
    }
  }
</script>



