<template>
  <div class="commonTop">
      <ul>
          <li v-for="(item,index) in toper"  v-if="index<6" :key="index"   >
            <lazy-image
                :src='item.elementImg'
                :placeholder='placeholder'
                  />
          </li>
      </ul>
      <div class="wordList">
          <ul>
              <li v-for="(item,index) in historyList" v-if="index<3" :key="index+6" >
                        <div class='s-title'>
                            <p class="s-name">{{item.assetName}}第{{item.episode}}集 {{formatDate(item.watchTime)}}</p><p class='s-name-temp'></p>
                        </div>
               </li>
          </ul>
          <div class="moreTeleplayHistory">
              <p>{{category[navpos].catName.substring(2,4)}}</p>
              <p>播放记录</p>
              <img src="../../assets/Intersection 3.png">
          </div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style>
    .commonTop{position: relative;width: 1160px;height:340px; }
    .commonTop li{width: 255px;height: 133px;float: left;margin-right:33px;margin-bottom:33px;}
    .commonTop li:nth-child(6){margin-right:-15px;}
    .commonTop li div{border:1px solid #95989A;border-radius: 19px;}
    .commonTop li img{width: 100%;height: 100%;border-radius:18px;}
    .wordList{position:relative;width: 543px;height:134px;text-align: left; border-radius: 16px;color:rgba(255, 255, 255, 0.14);font-size: 20px;letter-spacing: 1px;display:inline-block;}
    .wordList ul{position: relative;width:391px;height:134px;float: left;background-color: rgba(255, 255, 255, 0.14);border-radius:16px;margin-right: 12px;}
    .wordList li{color: #ffffff;width: 357px;height: 42px;margin:0px;padding:0px 15px;text-align:left;line-height:35px;}
    .moreTeleplayHistory{position: relative;width: 140px;height: 134px;background: linear-gradient(180deg,rgba(172, 74, 74, 1),rgba(70, 25, 25, 1));display: inline-block;border-radius:16px; }
    .moreTeleplayHistory p{position: relative;text-align:end;margin-right:10px;font-size: 22px;color: #ffffff;opacity: 0.89;}
    .moreTeleplayHistory p:nth-child(1){margin-top: 65px;}
    .commonTop li.hover{border:6px solid #FFD800;margin: -6px 27px 27px -6px;border-radius:24px;}
    .commonTop li:nth-child(6).hover{border:6px solid #FFD800;margin: -6px -21px -6px -6px;border-radius:24px;}
    .wordList li.hover{border:6px solid #FFD800;border-radius: 20px;margin:-6px !important;}
    .moreTeleplayHistory.hover{border:6px solid #FFD800;margin: -6px -6px -6px -6px;border-radius:24px;}
    .commonTop .s-title{position: relative;text-align:left;margin-top: 2px;color: #FFFFFF;width:359px;border:none;margin-left: 10px }
     .s-title{overflow: hidden;text-overflow:ellipsis;white-space: nowrap;text-align:center;}
    .s-name,.s-name-temp{position: relative;display: inline;}
    .s-name-temp{padding-left:25px;}
    .moreTeleplayHistory img{width: 70px;height: 85px;position: absolute;left: 0;top: 10px;}
</style>
<script>
import {bp as bi} from '../../js/ga.js'
import {mapGetters} from 'vuex'
import {client as yh} from '../../js/client.js'
import c from '../../js/common.js'
export default {
  props:['initData','catId'],
  name:"CategoryMovieTop",
  data(){
      return{
          eleId:'',
          toper:[],
          placeholder:require('../../assets/img_loading_352x190.png'),
          initScore:require('../../assets/corner_mark.png'),
          pos:0,
          itemNo:0,
          isShow:false,
          timer:0,
          historyList:[],
      }
  },created(){
      this.toper = this.initData.elementList;
      this.eleId = this.initData.specialId;
      this.initHistory();
  },activated(){
      $('.wordList .s-name-temp').text("");
      this.initHistory();
  },mounted(){

  },methods:{
      keyCode(kc) {
                if (kc == "KeyBack") {
                    this.back();
                }else if(kc == "right"){
                    this.right();
                }else if(kc == "left"){
                    this.left();
                }else if(kc == "down"){
                    this.down();
                }else if(kc == "up"){
                    this.up();
                }else if(kc == "KeyEnter"){
                    try {
                      this.pageId = this.catId;
                      let traceId = "|"+this.catId+"|"+this.toper[this.itemNo].elementId;
                         let traceType = "|1|";
                         bi.categoryClick(this.catId, this.toper[this.itemNo].elementId, 3, 1, this.catId, traceId, traceType);
                      } catch (error) {
                         console.log(error);
                    }
                    if(this.pos==0){
                        let url  =  "";
                        if(this.itemNo<6){
                            url = this.toper[this.itemNo].jsonUrl;
                            this.$router.push({name:'Detail',params:{jsonUrl:url,catCode:1,catId:this.catId,parent:1,parentId:this.pageId}});
                        }else{
                           let record = this.historyList[this.itemNo-6];
                           let jsonStr =  JSON.stringify({catId:record.catId,index:record.episode,startTime:record.watchTime,jsonUrl:record.layoutUrl});
                           yh.play(jsonStr);
                        }
                    }else{
                       this.$router.push({name:"history",params:{catCode:6,catId:202642}});
                    }
                }
    },
    back() {
      this.removeCss();
      this.isShow = false;
      this.itemNo = 0;
      this.pos =0;
      this.$emit('pressDown', this.isShow,this.eleId,"back");
},
    right(){
        this.removeCss();
        if(this.itemNo==6||this.itemNo==7||this.itemNo==8){
            this.pos=1;
            this.itemNo=0;
        }else{
            if(this.itemNo==5){
                if(this.historyList.length==0){
                    this.pos = 1;
                    this.itemNo=0;
                }else{
                    this.itemNo++;
                }
            }else{
                this.itemNo++;
            }
        }
        this.addCss();
    },left(){
        if(this.itemNo==0&&this.pos==0)return;
        this.removeCss();
        if(this.pos==1){
            if(this.historyList.length==0){
                this.pos=0;
                this.itemNo=5;
            }else{
                this.pos=0;
                this.itemNo=6;
            }

        }else if(this.itemNo>5&&this.itemNo<9){
            this.itemNo=5;
        }else{
            this.itemNo--;
        }
        this.addCss();
    },down(){
        this.removeCss();
        if(this.pos==0&&this.itemNo<2){
            this.itemNo+=4;
        }else if(this.itemNo==2){
            if(this.historyList.length==0){
                this.itemNo=5;
            }else{
                this.itemNo=6;
            }
        }else if(this.itemNo==3){
            this.pos=1;
            this.itemNo=0;
        }else if(this.itemNo>=6&&this.itemNo<8){
            if(this.historyList.length==(this.itemNo-5)){

            }else{
                this.itemNo++
            }
        }else{
            this.isShow = false;
            this.$emit('pressDown', this.isShow,this.eleId,"down");
            return;
        }
        this.addCss();
    },up(){
        this.removeCss();
        if(this.pos==0&&this.itemNo==4||this.itemNo==5){
            this.itemNo-=4;
        }else if(this.itemNo==6){
            this.itemNo=2;
        }else if(this.pos==1){
            this.pos=0;
            this.itemNo=3
        }else if(this.itemNo<9&&this.itemNo>6){
            this.itemNo--;
        }else{
            this.isShow = false;
            this.$emit('pressDown', this.isShow,this.eleId,"up");
            return;
        }
        this.addCss();
    },addCss(){
        if(this.pos==0){
             $('.commonTop li').eq(this.itemNo).addClass('hover');
             let elementLength = $('.commonTop li').eq(this.itemNo).text();
             if(this.itemNo>5&&this.itemNo<9&&elementLength.length>16){
                 let fs =  $('.commonTop li').eq(this.itemNo).text().length;
                 let id = '.wordList li';
                 let speed = 40;
                 let that = this;
                 let tab =   document.querySelectorAll(id+' .s-title')[this.itemNo-6];
                 let tab1 =  document.querySelectorAll(id+' .s-name')[this.itemNo-6];
                 let tab2 =  document.querySelectorAll(id+' .s-name-temp')[this.itemNo-6];
                 tab.style.textOverflow = 'clip';
                 tab2.innerHTML = tab1.innerHTML;
                 this.timer = setInterval(function(){
                   that.Marquee(tab,tab1,tab2)
                 },speed);
             }
        }else if(this.pos==1){
             $('.moreTeleplayHistory').addClass('hover');
        }
    },removeCss(){
        if(this.pos==0){
            $('.commonTop li').eq(this.itemNo).removeClass('hover');
            if(this.itemNo>5&&this.itemNo<9){
                 if(this.timer!=0){
                    window.clearInterval(this.timer);
                    let tab =  document.querySelectorAll('.wordList li .s-title')[this.itemNo-6];
                    document.querySelectorAll('.wordList li .s-name-temp')[this.itemNo-6].innerHTML="";
                    if(tab!=undefined){
                        tab.scrollLeft = 0;
                        tab.style.textOverflow = 'ellipsis';
                    }
                }
            }
           
        }else if(this.pos==1){
            $('.moreTeleplayHistory').removeClass('hover');
        }
    },updateStast(bl,pos,isEnd){
        this.isShow = bl;
        this.addCss();
        if(bl==true&&isEnd==false){
            let eleH = this.cEleOffsetTop('.commonTop li:eq('+this.itemNo+')');
            console.log("组件距离文档的高度:"+eleH);
            this.$emit('eleScroll',this.name,pos,eleH);
        }
    },Marquee(tab,tab1,tab2){
              if(tab2.offsetWidth - tab.scrollLeft <=0){
                  // tab.scrollLeft -= tab1.offsetWidth;
                  tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
              }else{
                  tab.scrollLeft++;
              }
    },cEleOffsetTop(ele){
            let fEleDistance = $(ele).offset().top;
             // console.log(this.name,"元素距离文档顶部的距离:"+(fEleDistance));
            // console.log(this.name,"元素距离文档顶部的高度:"+(fEleDistance+258));
            let realHeight = fEleDistance+133;
            return realHeight;
    },initHistory(){
        // 获取点播历史信息
       let arr =  yh.filterHistory(this.navpos);
       this.historyList = arr.reverse();
       console.log(this.historyList+'获取到的历史信息');
    },formatDate(mss) {
         return c.formatDate(mss);
     }
  },watch:{
      initData(){
          this.toper = this.initData.elementList;
          this.eleId = this.initData.specialId;
          this.initHistory();
      }
  },computed: {
      ...mapGetters([
        'category',
        'behindParams',
        'isHTTPS',
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
        'navpos'
      ])
    }
}
</script>



