<template>
    <div class="yt-detail">
        <div class="yt-detail-back">返回上一页</div>
        <div class="detail-title">
            <h3>{{detailInfo.name}}</h3>
             <p class="wordList">价格：{{detailInfo.price}}{{detailInfo.unit}}/次</p>
        </div>
        <ul class="s-button">
            <li>递交订单</li>
            <li>咨询商家</li>
        </ul>
        <div class="goods_detail">
            <p class="wordLi">{{detailInfo.memo}}</p>
        </div>
        <div class="watermark">家政保姆</div>
        <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    </div>
</template>
<style>
     #app{padding: 0px;width: 1280px;height: 720px;}
    .yt-detail{position: absolute;width: 1280px;height: 720px;}
    .yt-detail-back{position: absolute;display: block;height: 50px;left: 81px;top: 21px;border-radius: 25px;background-color:rgba(255, 255, 255, 0.18);color: #FFFFFF;min-width: 137px;font-size: 22px;text-align: center;line-height:50px }
    .yt-detail-back.hover{border:6px solid #FFD800;margin: -6px;}
    .detail-title{position: relative;margin-top: 110px;text-align: left;}
    .yt-detail h3{position: relative;margin-left: 80px;display:inline-block;width: 300px;height: 65px;line-height: 65px;font-size: 46px;letter-spacing: 1px;color: #FFFFFF;}
    .yt-detail .wordList{position: relative;display:inline-block;height: 29px;line-height: 29px;font-size: 21px;color: #FFFFFF;width:200px ;}
    .s-button{position: absolute;left: 81px;height:61px;width:650px;top: 242px; }
    .s-button li{width: 159px;height: 100%;border-radius: 15px;font-size:22px;text-align:center;line-height: 61px;float: left;top:242px;color:#FFFFFF;background-color:rgba(255, 255, 255, 0.3);margin-right: 53px;}
    .s-button li.hover{border:6px solid #FFD800;margin:-6px 47px -6px -6px;}
    .goods_detail .wordLi.hover{margin:-5px 47px -5px -5px;border:6px solid #FFD800;opacity: 1;}
    .goods_detail{position: absolute;left:81px;top: 356px;width: 1106px;height: 187px;}
     .goods_detail:after{
     clear: both;
     content: '';
     display: block;
     }
     .goods_detail .wordLi{background-color: #FFFFFF;border: #707070 1px solid;opacity: 0.4393;width: 100%;float: left;font-size: 25px;height: 100%;color: black;text-align: left;}
    .watermark{position: absolute;top: 636px;left: 26px;width: 184px;height: 65px;font-size:46px;color: rgba(255, 255, 255, 0.35);opacity: 0.3888;}
</style>
<script>
  import {ytone} from '../../../js/ytone.js'
  import c from '../../../js/common.js'
  import Toast from '../../../tools/toast/toast.js'
  import {bp as bi} from '../../../js/ga.js'
  export default {
  data(){
      return{
          isShow:true,
          pos:0,
          itemNo:0,
          detailInfo:{},
          vcode:"",
          facilitator_id:"",//  服务商ID
          clickSubmit:true,
          clickCall:true,
      }
  },created(){
      this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
      window.getOrderTime = this.getOrderTime;
      window.createOrders = this.createOrders;
      window.deviceCallTime = this.deviceCallTime;
      window.deviceCall = this.deviceCall;
      this.detailInfo = this.$route.params.productInfo;
      this.facilitator_id = this.$route.params.facilitator_id;

      console.log("服务商ID="+this.facilitator_id);
      this.vcode = c.getLocal('yt-vcode');
  },mounted(){
      this.addCss();
  },methods:{
      keyCode(kc) {
        if (kc == "KeyBack") {
            this.$router.go(-1);
        }else if(kc == "right"){
            this.right();
        }else if(kc == "left"){
            this.left();
        }else if(kc == "down"){
            this.down();
        }else if(kc == "up"){
            this.up();
        }else if(kc == "KeyEnter"){
            if(this.pos==2){
               this.$router.go(-1);
            }else if(this.pos ==1){
            
            }else if (this.pos ==0){
               if(this.itemNo==0){
                   if(this.clickSubmit){
                       this.clickSubmit=false;
                       ytone.getServerTime(ytone.keepTime,'getOrderTime');
                       let _this = this;
                       setTimeout(() => {
                           _this.clickSubmit=true;
                       }, 15000);
                   }else{
                       Toast("请勿重复提交,15s以后再试");
                   }
               }else{
                   if(this.clickCall){
                       this.clickCall = false;
                       ytone.getServerTime(ytone.keepTime,'deviceCallTime');
                       let _this = this;
                        setTimeout(() => {
                           _this.clickCall=true;
                       }, 15000);
                   }else{
                        Toast("请勿重复提交,15s以后再试");
                   }
               }
            }
        }
      },right(){
          if(this.pos==0){
              if(this.itemNo>0){
                  return false;
              }else{
                this.removeCss();
                this.itemNo++;
                this.addCss();
              };
          }
      },left(){
          if(this.pos==0){
              if(this.itemNo==0){
                  return false;
              }else{
                  this.removeCss();
                  this.itemNo--;
                  this.addCss();
              }
          }
          
      },up(){
          if(this.pos==1){
              this.removeCss();
              this.pos=0;
              this.addCss();
          }else if(this.pos==0){
              this.removeCss();
              this.pos=2;
              this.addCss();
          }
      },down(){
          if(this.pos==0){
              this.removeCss();
              this.pos=1;
              this.itemNo=0;
              this.addCss();
          }else if(this.pos==1){

          }else if(this.pos==2){
              this.removeCss();
              this.pos=0;
              this.addCss();
          }
      },addCss(){
          if(this.pos==0){
             $('.s-button li').eq(this.itemNo).addClass('hover');
          }else if(this.pos==1){
             $('.goods_detail .wordLi').eq(this.itemNo).addClass('hover');
          }else if(this.pos==2){
             $('.yt-detail-back').addClass('hover');
          }
      },removeCss(){
          if(this.pos==0){
              $('.s-button li').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==1){
              $('.goods_detail .wordLi').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==2){
              $('.yt-detail-back').removeClass('hover');
          }
      },getOrderTime(ob){
          if(ob.code==1000){
              const timespace = ob.no;
              const user = c.getLocal('yt-user');
              console.log(user);
              let userInfo = null;
              if(user){
                 userInfo =  JSON.parse(user);
              }else{
                 Toast("请先登录！");
              }
              const signature = ytone.md5(ytone.account + userInfo.mobile+ 11+ timespace+ this.facilitator_id + this.vcode +ytone.Key);
              let jsonOb = '{\"signature\":\"'+signature+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"callPhone\":\"'+userInfo.mobile+'\",\"callType\":11,\"facilitator_id\":\"'+this.facilitator_id+'\",\"service_ext\":\"'+this.detailInfo.memo+'\"}';
              ytone.ytonePost(ytone.createOrder,jsonOb,'createOrders');            
         }else{
             Toast("创建订单时间失败！");
         }
      },createOrders(jsonData){
             if(jsonData.code==1000){
                 const num = jsonData.no; 
                 console.log("订单号:"+num);
                 Toast({
                    message:"订单已创建成功,订单号："+num+",请您耐心等候工作人员上门。",
                    position:'bottom',
                    duration:'3000'
                 });
             }else{
                 Toast("订单创建失败!");
             }
      },deviceCallTime(ob){// 发起网络设备呼叫
            //"callType":11		1表示:紧急呼叫 2表示: 综合服务,3,咨询援通, 11 网购视购
            if(ob.code==1000){
                const timespace = ob.no;
                const callDevice = "s639963222";
                const signature = ytone.md5(ytone.account + callDevice+ 11+ timespace+ this.vcode +ytone.Key);
                let jsonOb = '{\"signature\":\"'+signature+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"callDevice\":\"'+callDevice+'\",\"callType\":11}';
                ytone.ytonePost(ytone.phone,jsonOb,'deviceCall');    
           }

      },deviceCall(jsonData){
          if(jsonData.code==1000){
              const no = jsonData.no;
              console.log("呼叫订单:"+no);
               Toast({
                    message:"正在接通服务，请耐心等候工作人员联系您。",
                    position:'bottom',
                    duration:'3000'
                 });
          }else{
              Toast("呼叫失败，请重试!");
          }
      }
  },beforeRouteLeave(to, from, next){
      try {
           this.$store.commit('GET_STARTTIME',new Date().getTime());
           let overTime = 0;
           overTime =  new Date().getTime() - this.httpResponseTime;
           let traceId="1|"+this.detailInfo.id;
           let traceType = "1|3"
           bi.closPage(traceId,traceType,31,this.detailInfo.id,0,0,0,overTime);
      } catch (error) {
           console.log(error);
      }finally{
           next();
      }
    }
}
</script>



