<template>
  <div class="yy-confirm" v-show="visible">
    <div class="bg"></div>
    <div class="yy-container" :style="{width: width}">
      <div class="header">
        <i class="iconfont" style="font-size: 24px;line-height: 50px">&#xe6a3</i>
        {{title.value}}
      </div>
      <slot name="footer">
        <div class="yy-footer" slot="footer">
          <a href="javscript:void(0)" class="yy-btn make-sure select-on">播放详情
            <i class="iconfont" style="font-size: 22px;line-height: 50px">&#xe98c</i>
          </a>
          <a href="javscript:void(0)" class="yy-btn cancel">删除视频
            <i class="iconfont" style="font-size: 22px;line-height: 50px">&#xe600</i>
          </a>
        </div>
      </slot>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>
<script type="text/javascript">
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {client as yh} from '../../js/client.js'
  export default {
    data :function() {
      return {
        width: {
          type: String,
          default: '360px'
        },
        title: {
          type: String,
          default: '三角警告标',
          value:'视频管理'
        },
        isShow:false,
        catCode:"",
        num:0,
        name:"HistoryFirm",
        jsonUrl:'',
        json:[],
        asID:'',
        usename:'',
        zt:true,
        visible: false,
        closeVisible: {
          type: Boolean,
          default: true
        }
      }
    },
    methods: {
      close () {
        this.$emit('update:visible', false)
      },
      showBox (){
        this.visible = true
      },keyCode(kc) {
        // console.log("父组件接收子组件触发事件传上来的值");
        if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        }else if(kc == "KeyEnter"){
          this.KeyEnter();
        } else if (kc == "KeyBack") {
          this.KeyBack();
        }
      },
      KeyBack(){
        this.visible = false;
        this.isShow = false;
        this.$emit("listernpopup",false,this.usename,'back');
      }
      ,KeyEnter(){
        this.visible = false;
        this.isShow = false;
          if (this.num==0){
//              this.$router.push({name:"Detail",params:{jsonUrl:this.jsonUrl,catCode:this.catCode,catId:this.catId}});
              let tempUrl = this.jsonUrl.replace('https://winnow-bds.yanhuamedia.tv','http://winnow-ds.yanhuamedia.tv');
              let time = this.timeT(this.json.watchTime);
              let obj = JSON.stringify({catId:this.json.catId,index:this.json.episode-1,startTime:time,jsonUrl:tempUrl});
              yh.play(obj);
              console.log(obj+'播放数据');
            this.$emit("listernpopup",false,this.usename,'key');
          }else {
              yh.removeHistorys(this.asID);
            this.$emit("listernpopup",false,this.usename,'keyEnter');
          }
      },
      timeT(time){
        let s = '';
        let hour = time.split(':')[0];
        let min = time.split(':')[1];
        let sec = time.split(':')[2];

        s = Number(hour*3600) + Number(min*60) + Number(sec);

        return s*1000;
      },
      right(){
        this.num++;
        if (this.num>1){
          this.num=1
        }
        $('.make-sure').removeClass('select-on');
        $('.cancel').addClass('select-on');
      },left(){
        this.num--;
        if (this.num<0){
          this.num=0
        }
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
      },
      updateStast(bl,url,Id,lx,name,type,urls){
        this.num=0;
        $('.cancel').removeClass('select-on');
        $('.make-sure').addClass('select-on');
        this.isShow = bl;
        this.visible = bl;
        this.jsonUrl =url;
        this.asID = Id;
        this.catCode = type;
        this.usename = name;
        this.json = urls;
        if (lx=="专题"){
          this.zt = true;
        }else {
          this.zt = false;
        }
      }
    },
    components: {
    }
  }
</script>
<style type="text/css" scoped="firm">
  .bounce-enter-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .bounce-leave-active {
    -webkit-transform: scale(2);
    -moz-transform: scale(2);
    -ms-transform: scale(2);
    transform: scale(2);
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
  }
  .yy-confirm {
    position: fixed;
    top: 0;
    left: 0;
    width: 1280px;
    height: 720px;
    z-index: 9999999;
  }
  .yy-confirm .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.3;
    z-index: 99999;
  }
  .yy-confirm .yy-container {
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    max-width: 1280px;
    z-index: 9999999999999;
    width: 600px;
    border: 1px solid white;
  }
  .yy-confirm .yy-container .header {
    height: 50px;
    padding-left: 30px;
    line-height: 50px;
    background-color:rgba(21, 48, 57, 0.9);
    border-bottom: 1px rgba(0,128,128,0.95) solid;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
    margin-top: 0px;
  }
  .yy-confirm .yy-container .yy-footer {
    background: rgba(34,78,96,0.9);
    height: 300px;
    box-sizing: border-box;
  }
  .yy-btn {
    text-decoration: none !important;
    border-radius: 14px;
    text-align: center;
    width: 30%;
    float: left;
    margin-top: 120px;
    margin-left: 80px;
    height: 50px;
    line-height: 50px;
    border: 2px white solid;
    background-color: #152F3A;
    color: #fff;
  }
  .yy-btn.select-on {
    background-color: #992121;
    color: #fff;
  }
  .yy-close-btn {
    cursor: pointer;
    float: right;
  }
</style>



