/**
 *  打开视频播放器
 * params:{int index,;long startTimeMills,String jsonUrl}
 *
 *    index当前集数：电影、非影视剧为0，电视剧从0开始
 *    startTimeMills：播放器开始播放的毫秒秒数
 *    jsonUrl:详情页数据接口
 */

yh.prototype.play = function (params) {
    try {
        let obj = JSON.parse(params);
        console.log(params);
        ERT.play(obj.catId,obj.index, parseInt(obj.startTime), obj.jsonUrl);
    } catch (error) {
        console.log("调用ERT.play()异常");
    }
};
