yh.prototype.filterHistory = function (catId) {
   // let tempArr = '[{"assetId":"1188","assetImg":"http://aged-s.yanhuamedia.tv//img/201610/18/14/56/201610181456585011a555475.jpg","assetName":"勇敢传说","assetType":"Movie","broadcast":"","catId":"4","episode":"1","episodes":"1","id":1,"itemId":"24077","itemImg":"http://aged-s.yanhuamedia.tv//img/201612/17/01/03/20161217010357512645077f2.jpg","itemName":"勇敢传说_国语版","layout":"Detail_Movie","layoutUrl":"https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=1188&c=6","movieLength":"5383840","playurl":"http://vcdn.yanhuamedia.tv/vod/201608/02/16/35/201608021635092423je9qwvq.ts","recordTime":1521022241737,"score":"7.4","specialId":"","watchTime":"1189605"},{ "assetId": "201666", "assetImg": "http://aged-s.yanhuamedia.tv//img/201803/08/16/39/0/201803081639006017520f677.jpg", "assetName": "亮剑", "assetType": "Series", "broadcast": "", "catId": "1", "episode": "1", "episodes": "30", "id": 2, "itemId": "254399", "itemImg": "", "itemName": "亮剑第1集", "layout": "Detail_Series", "layoutUrl": "https://aged-bds.yanhuamedia.tv/?s=17|6&p=yhAssetDetail&k=1&v=1&assetId=201666&c=6", "movieLength": "2614560", "playurl": "http://vcdn.yanhuamedia.tv/vod/201801/24/22/03/201801242203481894299ad01.ts", "recordTime": 1521022550756, "score": "9.0", "specialId": "", "watchTime": "32100" }]';
    let resultArr = [];
    let jsonObject = [];
    try {
        let tempArr = ERT.queryHistory();
        jsonObject = JSON.parse(tempArr);
        if (jsonObject.length > 0) {
            jsonObject.forEach(element => {
                if (element.catId == catId) {
                    resultArr.push(element);
                }
            });
        }
    } catch (error) {
        console.log("筛选历史数据异常");
    }

    return resultArr;
};

