<template>
  <div class="userInfo">
    <ul>
      <li>姓名:<p>{{userInfo.name}}</p></li>
      <li>出生日期:<p>{{userInfo.birthday}}</p></li>
      <li>年龄:<p>{{userInfo.age}}</p></li>
      <li>身高:<p>{{userInfo.height}}</p></li>
      <li>性别:<p>{{userInfo.gender==1?'男':'女'}}</p></li>
      <li>联系电话:<p>{{userInfo.mobile}}</p></li>
      <li>居中地址:<p>{{userInfo.addr}}</p></li>
    </ul>
    <div class="userQrcode" v-if="false">
      <img src="../../../assets/img_loading_352x190.png" />
      <Load :isload="show"></Load>
    </div>
  </div>
</template>
<style scoped>
  .userInfo{position: relative;margin-top: 62px;margin-left: 65px;text-align: left;}
  .userInfo li{height:45px;width:auto;margin-bottom:28px;  font-family: 'Helvetica Neue','Condensed Bold';font-size: 32px;letter-spacing: 1px;color: #989DAA;}
  .userInfo li p{display: inline;color: #FFFFFF;}
  .userInfo li:nth-child(1),.userInfo li:nth-child(3),.userInfo li:nth-child(4),.userInfo li:nth-child(5){margin-left: 65px;}
  .userQrcode{position:absolute;width: 204px;height: 204px;top: -12px;left: 677px;background:#999DAB;}
  .userQrcode img{width: 100%;height: 100%;}
  .loader {
        position: relative;
        margin-top: -204px;
        width: 184px;
        height: 182px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center
    }
</style>
<script>
import Load from '../../../tools/loading/Loader'
import { ytone } from '../../../js/ytone';
import c from '../../../js/common.js'
import Toast from '../../../tools/toast/toast.js'


export default {
  data(){
    return{
      show:true,
      vcode:'',
      userInfo:{
        "code":1000,
        "msg":"",
        "no":"",
        "addr":"",
        "age":"",
        "birthday":"",
        "bphigh":"",
        "bplow":"",
        "bpstarttime":"",
        "bse":"",
        "bsf":"",
        "gender":0,
        "height":0,
        "mobile":"",
        "name":"",
        "qrcode":"",
        "weight":0,
      }
    }
  },created(){
    this.vcode = c.getLocal('yt-vcode');
    window.userloginTimeCallback = this.userloginTimeCallback;
    window.userTime = this.userTime;
    window.userLoginCallback = this.userLoginCallback;
    window.queryUser = this.queryUser;
    if(c.isAllNull(this.vcode)){
          // 暂无vcode 需登录
          ytone.getServerTime(ytone.keepTime,'userloginTimeCallback');
      }else{
          //  获取用户信息
          console.log("已有vcode信息");
          ytone.getServerTime(ytone.keepTime,'userTime');
      }
  },methods:{
    userloginTimeCallback(ob){
            if(ob.code==1000){
                let timespace= ob.no;
                let pwd = ytone.Pwd.toLowerCase();
                let key = ytone.Key.substring(0,8);
                let desPwd = ytone.descEncode(key,pwd);
                let MD5String = ytone.md5(ytone.account+ytone.Pwd+timespace+ytone.Key).toUpperCase();
                let jsonOb = '{\"signature\":\"'+MD5String+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"password\":\"'+desPwd+'\"}';
                ytone.ytonePost(ytone.loginUrl,jsonOb,'userLoginCallback');
            }else{
                Toast("登录援通账号失败!");
            }
    },userLoginCallback(info){
      if(info.code==1000){
            console.log("援通账号，登录成功！");
            c.setLocal('yt-vcode',info.vcode);
            this.vcode = info.vcode;
            ytone.getServerTime(ytone.keepTime,'userTime');
        }else{  // 登录失败
            Toast("援通账号登录失败,请联系客服!")
        }
    },
    userTime(ob){
      if(ob.code==1000){
        const deviceNo = "s639963222";
        console.log("设备号:"+deviceNo);
        const timespace = ob.no;
        let md5 = ytone.md5(ytone.account+deviceNo+timespace+ytone.Key+this.vcode).toUpperCase();
        let jsonOb = '{\"signature\":\"'+md5+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"deviceNo\":\"'+deviceNo+'\",\"vcode\":\"'+this.vcode+'\"}';
        ytone.ytonePost(ytone.devicesInfo,jsonOb,'queryUser');
     }else{
        Toast("获取用户信息失败");
     }
    },queryUser(jsonData){
        console.log("YT return："+JSON.stringify(jsonData));
        if(jsonData.code==1000){
            this.userInfo = jsonData;
            c.setLocal('yt-user',JSON.stringify(jsonData));
        }
    }
  },
  components:{Load}
}
</script>



