<!--重用详情页-->
<style scoped="GameP">
  .back{
    width: 137px;
    height: 50px;
    background: rgba(255,255,255,0.4);
    text-align: center;
    line-height: 50px;
    margin-bottom: 11px;
    border-radius: 15px;
    font-size: 20px;
    color: rgba(255,255,255,0.7);
  }
  .time{
    font-size: 20px;
    float: right;
    margin-right: 60px;
    line-height: 60px;
    position: absolute;
    right: 50px;
    top: -50px;
    color: rgba(255,255,255,0.7);
  }
  .box{
    /*background: linear-gradient(to bottom,#A8977C 0,#A8977C 100%);*/
    position: relative;
    width: 1200px;
    height: 720px;
    overflow: hidden;
    margin-top: 21px;
    margin-left: 79px;
  }
  .box .scroll-box{
    width: 1200px;
    position: absolute;
    left: 0px;
    top: 0px;
    transform:inherit;
  }
  .time{
    font-size: 20px;
    float: right;
    margin-right: 60px;
    line-height: 60px;
    position: absolute;
    right: 40px;
    top: -60px;
    color: rgba(255,255,255,0.7);
  }
  .backchange{
    background: #F36100;
    border: solid 4px #F8D203;
    line-height: 42px;
    box-sizing: border-box;
  }
</style>
<template>
  <div class="box">
    <div class="scroll-box">
      <p class="time"> </p>
      <div class="back">{{backing}}</div>
      <component v-for="(item,index) in child" :is="item.ele" :key="index" v-on:pressDown="keepDo"
                 v-on:eleScroll="changeHeight" v-on:alertM="openMsg" ref="childDo" :initData="item.initData" :jsonUrl="childrenUrl" :catId="traceId"
                 :catCode="traceType" :parent="3" :parentId="pageId" :navpos="navpos"></component>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <Detailfirm ref="Detailfirm" :isAlert="alertMsg" v-on:press="kDo"></Detailfirm>
    <LFirm v-if="ShowLFirm"></LFirm>
  </div>
</template>
<script>
  import DetailTop from '../detail/DetailTop.vue'
  import DetailList from '../detail/DetailList.vue'
  import DetailRecom from '../detail/DetailRecom.vue'
  import Detailfirm from '../detail/Detailfirm.vue'
  import DetailListOne from '../detail/DetailListOne.vue'
  import {client as yh} from '../../js/client.js'
  import {mapGetters} from 'vuex'
  import {HTTPUtil as api} from '../../fetch/api.js'
  import {bp as bi} from '../../js/ga.js'
  var itemNo=0;
  export default {
    props:["jsonUrl","catCode","catId","parent","parentId"],
    data() {
      return {
        isShow:false,
        backing:"返回上一页",
        currentView: '',
        currentC: 0,
        nummc:0,
        ShowLFirm:true,
        componentsName: [],
        child: [],
        componentsId: [],
        cheight:[],
        special:[],
        pageSize: 0,
        pageId:0,
        old:0,
        oldTimes:0,
        oldTime:0,
        childrenUrl:"",
        traceId:0,
        traceType:"",
        alertMsg:{
          title:"",
          msg:"",
          alert:false
        },
      }
    },
    created() {
      this.init();
      console.log("详情页加载数据......");
      this.show = false;
    },
    updated(){

    },
    methods:{
      getTimes(gettime){
        let newtime =new Date(new Date().setHours(0,0,0,0));
        var timestamp = Date.parse(newtime);
        let timeout = Math.ceil((timestamp-gettime)/86400000);
        console.log(timestamp+'当天凌晨时间');
        return timeout;
      },
      keyCode(kc) {
        if (kc == "KeyBack") {
          if(this.$route.query.jsonUrl!=undefined){
            yh.back();
          }else{
            this.$router.go(-1);
          }
        } else if (kc == "down") {
          $('.back').removeClass('backchange');
          this.isShow = false;
          let childComponents = this.$refs.childDo;
          childComponents[0].updateStast(true, "up", false);
          this.pageSize = 0;
          this.cheight = [];
        }else if(kc=='KeyEnter'){
          if(this.$route.query.jsonUrl!=undefined){
            yh.back();
          }else{
            this.$router.go(-1);
          }
        }
      },
      load(){
        //埋点大数据
        if(this.nummc<2){
          this.oldTime =performance.timing.domContentLoadedEventEnd-performance.timing.navigationStart;
          console.log(this.oldTime+'=====页面第一次加载时间');
        }
        this.pageId = this.special.assetId;
        this.traceId = this.catId+"|"+this.pageId;
        this.oldTimes = (new Date()).valueOf();
        this.traceType = "|"+this.catCode+"|3";
        bi.loadPage(this.traceId,'',31,this.pageId,'','','',this.oldTime);
        console.log('信息流'+this.traceId,this.traceType,31,this.pageId,this.parent,this.parentId,0,this.oldTime);
      },
      closPages(){
        //      // 离开页面（页面停留时间）
//      bp.closPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, special_type, oldTime)
        let time = (new Date()).valueOf();
        let times = time - this.oldTimes;
        this.pageId = this.special.assetId;
        this.traceId = this.catId+"|"+this.pageId;
        this.traceType = "|"+this.catCode+"|3";
        console.log(this.pageId+'=====this.pageId退出刷新ID');
        console.log('this.old电影退出停留本页面时间===='+times);
        bi.closPage(this.traceId,this.traceType,31,this.pageId,this.parent,this.parentId,0,times);
      },
      init(){
        let curl;
        if (this.$route.query.jsonUrl) {      
          curl = this.$route.query.jsonUrl + this.behindParams;
          this.childrenUrl = this.$route.query.jsonUrl;
        } else {
          if(this.jsonUrl){
            this.childrenUrl = this.jsonUrl;
            curl = this.jsonUrl + this.behindParams;
            localStorage.setItem('address',this.jsonUrl);
          } else {
            curl = localStorage.getItem('address') || '';
            this.childrenUrl = curl;
            curl+=this.behindParams;
          }
        }
        this.getDetail(curl);
      },
      keepDo(bl, name, pos,url) {
        let childComponents = this.$refs.childDo;
        let componentsSize = childComponents.length;
        for (let i = 0; i < childComponents.length; i++) { //遍历组件ref 给对应的组件排序
          if (name == childComponents[i].name) {// 当对应组件上下没有焦点时会抛出控制权，此时父组件需要根据子组件抛出的信息来决定控制权交给其他父组件
            let _num = this.componentsId.indexOf(name);
            if (pos == "down") {
              if(_num==componentsSize-1) {
                this.getChildRef(name).updateStast(true, pos, true);
              }else{
                let _cdown = this.componentsId[_num+1];
                this.getChildRef(_cdown).updateStast(true, pos, false);
              }
            } else if (pos == "up") {
              if (_num == 0) {
                $('.scroll-box').animate({'top': '0px'},100);
                $('.back').addClass('backchange');
                this.isShow = true;
              } else {
                let _cdown = this.componentsId[_num-1];
                this.getChildRef(_cdown).updateStast(true, pos, false);
              }
            }
            if(pos=='KeyEnter'){
              this.childrenUrl = url;
              this.getDetail(url+this.behindParams);
              if (this.child[0].initData.data.itemList[1]){
                childComponents[1].updateStast(true, pos, false);
                $('.scroll-box').stop(false,true).animate({'top':'0px'},100);
                this.pageSize = 1;
                this.cheight = [];
//              }else if(this.child[0].initData.data.assetType=="Series"){
//                childComponents[1].updateStast(true, pos, false);
//                $('.scroll-box').stop(false,true).animate({'top':'0px'},100);
//                this.pageSize = 1;
//                this.cheight = [];
              }else{
                $('.scroll-box').stop(false,true).animate({'top':'0px'},100);
                childComponents[0].updateStast(true, pos, false);
                this.pageSize = 0;
                this.cheight = [];
              }
            }
            break; // 找到组件需终止循环foreach则不会，需要抛异常才会终止
          }
        }
      },
      getChildRef(id){// 根据元素ID查找ref中的组件
        let childComponents = this.$refs.childDo;
        for(let i=0;i<childComponents.length;i++){
          if(id==childComponents[i].name){
            return childComponents[i];
          }
        }
      },
      kDo(bl,name,pos){
        let childComponents = this.$refs.childDo;
        if (pos=='KeyBack') {
          childComponents[0].updateStast(true, 'up',false);
        }
      },
//      滚动方法
      changeHeight(name, pos, height) {
        if(pos=='down'){
          if(height>710){
            let _temp = height-700;
            this.cheight.push(_temp);
            $('.scroll-box').stop(false,true).animate({'top': '-='+_temp},100);
            this.pageSize++;
          }
        }else if(pos=='up'){
          if(this.pageSize == 0){
            this.pageSize=0;
            $('.scroll-box').stop(false,true).animate({'top':'0px'},100);
          }else {
            this.pageSize -= 1;
            $('.scroll-box').stop(false,true).animate({'top':'+='+this.cheight[this.pageSize]},100);
            this.cheight.splice((this.pageSize),1);
          }
        }
      },
      openMsg(obj){
        this.alertMsg = obj;
      },
      ds() {
        setInterval(function () {
          let date = new Date();
          let h = date.getHours(); //获取小时
          let m = date.getMinutes(); //获取分钟
          if (h<10){
            $('.time').text('0' +h +':'+  m );
          }else {
            $('.time').text(h +':'+  m  );
          }
          if (m<10){
            $('.time').text(h + ':0' + m );
          }else {
            $('.time').text(h + ':' + m );
          }
        }, 1000)
      },
//      页面获取数据
      getDetail(curl){
        if(this.nummc>=1){
          let time = (new Date()).valueOf();
          let times = time - this.oldTimes;
          this.pageId = this.special.assetId;
          this.traceId = this.catId+"|"+this.pageId;
          this.traceType = "|"+this.catCode+"|3";
          console.log(this.pageId+'=====this.pageId退出刷新ID');
          console.log('this.old电影未退出停留本页面时间===='+times);
          bi.closPage(this.traceId,this.traceType,31,this.pageId,this.parent,this.parentId,0,times);
        }
        this.old = (new Date()).valueOf();
        let that = this;
        if(this.isHTTPS && curl.indexOf('https')<0 ){
          curl =  curl.replace('http','https');
        }
        api.get(curl).then((response)=>{
          let that =this;
          let timeSs = performance.timing.domContentLoadedEventEnd-performance.timing.navigationStart;
          console.log(timeSs+'假造加载时间');
          if(timeSs>2000 || timeSs == 0){
            timeSs = 2000;
          }
          setTimeout(function () {
            that.$loading.close();
          },timeSs);
          let components = [];
          let componentsName = [];
          that.special= response.data;
          let specialList = response.data;
          components.push({ele: 'DetailTop', initData: response})
          componentsName.push('DetailTop');
          if (specialList.itemList[1]) {
            components.push({ele: 'DetailList', initData: response})
            componentsName.push('DetailList');
          }
//          else{
//            components.push({ele: 'DetailListOne', initData: response})
//            componentsName.push('DetailListOne');
//          }
          if (specialList.assetList[0]) {
            components.push({ele: 'DetailRecom', initData: response})
            componentsName.push('DetailRecom');
          }
          that.child = components;
          that.componentsId = componentsName;
          this.nummc ++;
          if(this.nummc>1){
            let time = (new Date()).valueOf();
            that.oldTime = time-that.old;
            console.log(that.oldTime+'=====页面第二次加载时间');
          }
          that.$nextTick(function(){
            if (this.child[0].initData.data.itemList[1]==undefined){
                setTimeout(() => {
                  this.$refs.childDo[0].updateStast(true, "indown", true);
                }, 0);
            }
          })
        },(response)=>{
          console.log("获取详情页数据失败");
        })
      }
    },
    computed: {
      ...mapGetters([
        'category',
        'behindParams',
        'isHTTPS',
        'navpos'
      ])
    },
    components: {
      DetailTop,DetailList,DetailRecom,Detailfirm,DetailListOne
    },
    beforeDestroy: function () {
      console.group('beforeDestroy 销毁前状态电影===============》');
      this.closPages();
    },
    watch:{
      special(){
        this.$nextTick(function(){
          this.load();
          this.ShowLFirm = false;
        })
      }
    }
  }
</script>




