<template>
  <div class="ordersList">
      <div class="order" v-for="(item,index) in historyList" :key="index">
          <div class="order-mark">{{item.status==0?"未分配":""}}{{item.status==1?"完结":""}}{{item.status==2?"已分配":""}}{{item.status==3?"已安排":""}}</div>
          <div class="oreder-title">
              <h2>{{item.serial_number}}</h2><h3>{{item.call_type}}</h3>
          </div>
          <div class="OrderNode">
              <span>客户呼入</span><span>需求调度</span><span>服务安排</span><span>完结</span>
          </div>
          <div class="progressBar">
            <!-- 进度条 -->
            <div><span :data-id="index" v-bind:style="classObject(index)" ></span></div>
            <!-- 四个圆 -->
            <span></span> <span :style="nodeColor(index,0)"></span> <span :style="nodeColor(index,1)"></span> <span :style="nodeColor(index,2)"></span>
          </div>
          <div class="order-time">
              <span>{{item.call_time}}</span><span>{{item.processing_items==""?"":item.processing_items[0].answer_time}}</span><span>{{item.processing_items==""?"":item.processing_items[0].complete_time}}</span><span>{{item.complete_time}}</span>
          </div>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style>
    .ordersList{position: relative;margin:71px 80px 0px 40px;transform-style: preserve-3d;backface-visibility: hidden;perspective: 1000;}
    .order{position: relative;width: 925px;height: 256px;border-radius:15px;color: #ffffff;border:1px solid #707070;display:inline-block;background-color:rgba(76, 83, 108, 1); opacity:1;margin-bottom: 38px;}
    .order-mark{position: absolute;right:0px;width: 100px;height: 36px;background-color:#BF6565;border-radius:0px 11px 0px 11px;font-size: 20px;font-family:Helvetica Neue,Regular;letter-spacing: 1px;line-height: 35px; }
    .oreder-title{position: relative;display: inline-block;width: 100%;margin: 25px 0px 0px 21px;text-align: left; }
    .oreder-title h2{height: 35px;display: inline-block;margin-right:25px; }
    .oreder-title h3{height: 35px;display: inline-block;font-size: 21px;opacity: 0.5683;}
    .progressBar{width: 80%;height: 20px;position: relative;margin: 24px 0 0 95px;}
    .progressBar div{width: 92%;height: 4px;position: absolute;top:50%;left: 0;margin-top:-10px;background: #FFFFFF;}
    .progressBar div span{position: absolute;display: inline-block;background:#FFF500;left:0px;height: 4px;width: 0%;}
    .progressBar>span{position: absolute;top:0;margin-top: -8px;width: 20px;height: 20px;border-radius: 50%;background:#FFF500;margin-left: -20px;color:#FFFFFF;}
    .progressBar>span:nth-child(2){left: 0%;background:#FFF500;}
    .progressBar>span:nth-child(3){left: 33%;background:#FFFFFF;}
    .progressBar>span:nth-child(4){left: 64%;background:#FFFFFF;}
    .progressBar>span:nth-child(5){left: 93%;background:#FFFFFF;}
    .OrderNode{text-align: left;font-size: 25px;font-family: PingFang HK, Medium;margin-top: 20px;}
    .OrderNode>span{position: relative;width: 100px;height: 20px;color:#fff;}
    .OrderNode>span:nth-child(1){left: 5%;color:#FFF500;}
    .OrderNode>span:nth-child(2){left: 19%;color:#FFFFFF;}
    .OrderNode>span:nth-child(3){left: 33%;color:#FFFFFF;}
    .OrderNode>span:nth-child(4){left: 48%;color:#FFFFFF;}
    .order-time{text-align: left;font-size: 20px;font-family: PingFang HK, Medium;margin-top: 5px;opacity: 0.46;}
    .order-time>span{position: relative;width: 200px;height: 20px;color:#fff;text-align: center;display:inline-block;margin-right:23px;}
    .order.hover{background-color:rgba(201, 116, 0, 0.57);border:6px solid #FFD800;margin: -5px -5px 33px -5px;}
</style>
<script>
import {ytone} from '../../../js/ytone.js'
import c from '../../../js/common.js'
import Toast from '../../../tools/toast/toast.js'
export default {
  props:["iskeyDo"],
  data(){
      return{
          isShow:false,
          pos:0,
          itemNo:0,
          ordersNum:4,
          vcode:'',
          num:0,
          keepHeight:[],
          historyList:[],
          currpage:1,
          pagesize:12,
          recordcount:0,

      }
  },created(){
      this.vcode = c.getLocal('yt-vcode');
      window.keepOrderTime = this.keepOrderTime;
      window.historyOrder = this.historyOrder;
      ytone.getServerTime(ytone.keepTime,'keepOrderTime');
  },mounted(){

  },computed:{
      
  },methods:{
      keyCode(kc) {
       if($('.ordersList').is(':animated'))return;
       if (kc == "KeyBack") {
            this.$router.go(-1);
        }else if(kc == "right"){
                    this.right();
        }else if(kc == "left"){
                    this.left();
        }else if(kc == "down"){
                    this.down();
        }else if(kc == "up"){
                    this.up();
        }else if(kc == "KeyEnter"){

        }
      },up(){
          if(this.itemNo==0)return;
          this.removeCss();
          this.itemNo--;
           if(this.itemNo>0){
               $('.ordersList').stop(true,true).animate({'top':'+=300px'},200);
           }
           this.addCss();
      },down(){
          if(this.itemNo==(this.historyList.length-1))return;
          this.removeCss();
          this.itemNo++;
          if(this.itemNo>1){
              $('.ordersList').stop(true,true).animate({'top':'-=300px'},200);
          }
          if(this.itemNo%10==0){
              this.currpage++;
              this.pagesize+=10;
               ytone.getServerTime(ytone.keepTime,'keepOrderTime');

          }
          this.addCss();
      },left(){
          this.currpage=1;
          this.pagesize=20;
          this.itemNo=0;
          $('.ordersList').stop(true,true).animate({'top':'0px'},200);
          this.removeCss();
          this.isShow = false;
          this.$emit('menu', this.isShow,this.eleId,"down");
      },right(){

      },addCss(){
          if(this.pos==0){
              $('.order').eq(this.itemNo).addClass('hover');
          }
      },removeCss(){
          if(this.pos==0){
               $('.order').eq(this.itemNo).removeClass('hover');
          }
      },keepOrderTime(ob){
          const _temp= c.getLocal('yt-user');
          const user = JSON.parse(_temp);
          if(ob.code=1000){
              const timespace = ob.no;
              const pagesize = this.pagesize;
              const currpage = this.currpage;
              const signature = ytone.md5(ytone.account + pagesize + currpage + timespace  + this.vcode+ ytone.Key);
              let jsonOb = '{\"signature\":\"'+signature+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"currpage\":\"'+currpage+'\",\"pagesize\":\"'+pagesize+'\",\"items\":[{\"coperator\":1,\"cvalue\":\"'+user.mobile+'\",\"field\":\"call_number\"}]}';
              ytone.ytonePost(ytone.orderList,jsonOb,'historyOrder');
          }
      },historyOrder(jsonData){
          console.log('yt-return:'+JSON.stringify(jsonData))
          if(jsonData.code==1000){
              this.recordcount = jsonData.recordcount;
               this.historyList = this.historyList.concat(jsonData.items);
          }
      },classObject(num){
          // 工单状态,0表示未分配呼;1表示已处理完结;2表示系统已经将呼叫分配给监控座席;3表示分配给具体服务商
          const s = this.historyList[num].status;
          let _width = "width:0%";
          if(s==0){
              _width="width:0%";
          }else if(s==1){
            _width ="width:100%";
          }else if(s==2){
              _width = "width:35%";
          }else if(s==3){
              _width ="width:70%";
          }
          return _width;
      },nodeColor(index,num){
           const s = this.historyList[index].status;
           const nodebg = "color:#FFF500;background:#FFF500";
           const initbg = "color:#FFFFFF;background:#FFFFFF";
           if(s==0){
               if(num==0||num==1||num==2){
                   return initbg;
               }
            }else if(s==1){
               if(num==0||num==1||num==2){
                   return nodebg;
               }
            }else if(s==2){
              if(num==0){
                   return nodebg;
               }else{
                   return initbg;
               }
            }else if(s==3){
                if(num==0 || num==1){
                   return nodebg;
               }else{
                   return initbg;
               }
            }
      }
  },watch:{
      iskeyDo(){
          if(!this.iskeyDo){
              this.isShow = !this.iskeyDo;
              this.addCss();
          }
      }
  }
}
</script>



