<template>
  <div class="serviceBox">
      <div class="serviceCategory">
          <div class="yt-back">返回上一页</div>
          <ul class="serviceName">
              <li v-for="(item,index) in services" :key="index" :id="('services-'+index)"><span :id="('yt-services-'+index)">{{item.name}}</span><span :id="('yt-copy-'+index)" v-show="index==toScroll" style="padding-left:10px">{{item.name}}</span><div class="lfocus"><span class="l-arrows"></span></div></li>
          </ul>
          <div class="watermark">居家服务</div>
      </div>
      <div class="Retail">
          <ul class="familyService">
              <li v-for="(item,index) in section" :key="index">
                  <div class="goods">
                      <img :src="placeholder"/>
                      <div class='g-title'>{{item.name}}</div>
                  </div>
                  <p>￥{{item.price}}</p>
              </li>
          </ul>
      </div>      
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
    #app{padding: 0px;width: 1280px;height: 720px;}
    .serviceCategory,.Retail{display: inline-block}
    .serviceCategory{position: relative;width: 340px;height: 720px;float: left;border-radius: 0px 15px 0px 0px;background-color: rgba(53, 66, 96, 1);box-shadow: 0px 3px blur 6px;}
    .Retail{position: relative;width:845px;height: 720px;}
    .yt-back{position: absolute;top: 21px;left: 81px;width: 137px;height: 50px;line-height:50px;border-radius: 25px;background-color: rgba(255, 255, 255, 0.18);color: rgba(255, 255, 255, 0.7);font-size: 22px;letter-spacing: 1px;}
    .yt-back.hover{border:6px solid #FFD800;margin: -6px;}
    .watermark{position: absolute;top: 636px;left: 26px;width: 184px;height: 65px;font-size:46px;color: rgba(255, 255, 255, 0.35);opacity: 0.3888;}
    .serviceName{position: absolute;width: 220px;height: 500px;left: 55px;top:124px;font-size:25px;color: #FFFFFF;}
    .serviceName li{margin-bottom:32px;opacity: 0.7372;letter-spacing: 1px;padding: 3px;height: 36px;line-height:36px;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;}
    .copyName{display: none}
    .familyService{position: absolute;left:58px;top:130px;width: 830px;height:606px;display: inline-block;}
    .familyService li{position:relative;width: 196px;height:155px;margin-bottom: 66px;margin-right: 77px;float: left;border:rgba(149, 152, 154, 1) 1px solid;border-radius: 16px;}
    .familyService img,.familyService .goods{width: 100%;height: 100%;border-radius: 15px;}
    .g-title{position: absolute;display: inline-block;z-index: 99;width: 95%;height: 45px;bottom: 0;left:0px;padding-top: 5px;padding-left: 10px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;background-color:rgba(41, 160, 156, 0.69);color:#FFFFFF;font-size:25px;text-align:left;border-radius: 0px 0px 15px 15px;}
    .familyService li p{font-size: 25px;text-align: center;color: #FFFFFF;padding-top: 5px;}
    .serviceName li.color{color: #FFFFFF;text-shadow: 0px 3px 20px rgba(255,255,255,1);opacity: 1;}
    .serviceName li.hover{background-color:#C97400;border:6px solid #FFD800;color: #FFFFFF;opacity: 1;border-radius:15px;margin: -6px -6px 26px -6px;  }
    .lfocus{position: absolute;width: 3px;height: 45px;border-right: #FFFFFF 3px solid;right: 0px;margin-top: -37px;margin-right: -36px;border-radius: 10px 0px 0px 10px;display: none;}
    .l-arrows{display: block;width: 0;height: 0;border-width: 5px 5px 5px 0;border-style: solid;border-color: transparent rgba(255, 255, 255, 1) transparent transparent;position: absolute;top: 16px;left: -2px;}
    .familyService li.hover{border:6px solid #FFD800;margin: -5px 72px 61px -5px;border-radius: 21px;}
</style>
<script>
import {ytone} from '../../../js/ytone.js'
import c from '../../../js/common.js'
import Toast from '../../../tools/toast/toast.js'
import {mapGetters} from 'vuex'
import {bp as bi} from '../../../js/ga.js'

export default {
  data(){
      return{
           placeholder:require('../../../assets/img_loading_160x230.png'),
           initScore:require('../../../assets/corner_mark.png'),
           pos:0,
           itemNo:0,
           isShow:true,
           lastF:0,
           vcode:"",
           pageSize:0,
           mh:[],
        //    services:[{"adrr":"大奇山路口90-98号       一品江山大门口","am":8,"contactname":"徐爱萍","id":838845,"is_holiday":1,"mobile":"64663382","name":"桐庐创世纪爱老服务中心","pm":17},{"adrr":"电视平台生活版块服务","am":9,"contactname":"孙大","id":838782,"is_holiday":0,"mobile":"18958017777","name":"援通电视生活服务","pm":18},{"adrr":"开元街90号","am":0,"contactname":"援通商行","id":838760,"is_holiday":1,"mobile":"18958017777","name":"援通商行","pm":0}],
           services:[],
           cid:0,
           firstLoad:true,
           section:[],
           sort:[],
           // section:[[{"id":7490,"memo":"提前预约什么时间：烧饭服务","name":"烧饭","price":"0","unit":"小时"},{"id":7491,"memo":"提前预约什么时间服务：洗脚服务","name":"洗脚","price":"00","unit":"小时"},{"id":7492,"memo":"提前预约什么时间服务：洗头服务","name":"洗头","price":"00","unit":"小时"},{"id":7493,"memo":"提前预约什么时间服务：送饭服务","name":"送饭","price":"00","unit":"小时"},{"id":7494,"memo":"提前预约什么时间服务：陪聊服务","name":"陪聊","price":"00","unit":"小时"},{"id":7495,"memo":"提前预约什么时间服务：代购服务","name":"代购服务","price":"00","unit":"小时"},{"id":7496,"memo":"提前预约什么时间服务：铺床服务","name":"铺床","price":"00","unit":"小时"},{"id":7497,"memo":"提前预约什么时间服务：洗衣服服务","name":"洗衣服","price":"00","unit":"小时"},{"id":7498,"memo":"提前预约什么时间服务：搞卫生服务","name":"搞卫生","price":"0","unit":"小时"},{"id":7499,"memo":"提前预约什么时间服务：量血压服务","name":"量血压","price":"00","unit":"小时"},{"id":7500,"memo":"提前预约什么时间服务：剪头发服务","name":"剪头发","price":"00","unit":"小时"},{"id":7501,"memo":"提前预约什么时间服务：梳头发服务","name":"梳头发","price":"00","unit":"小时"},{"id":7502,"memo":"提前预约什么时间服务：洗被子服务","name":"洗被子","price":"00","unit":"小时"},{"id":7503,"memo":"提前预约什么时间服务：陪看病服务","name":"陪看病","price":"00","unit":"小时"},{"id":7505,"memo":"提前预约什么时间服务：缝衣服服务","name":"缝衣服","price":"00","unit":"小时"},{"id":7506,"memo":"提前预约什么时间服务：修剪指甲服务","name":"修剪指甲","price":"00","unit":"小时"},{"id":7507,"memo":"提前预约什么时间服务：代缴水、电费服务","name":"代缴水、电费","price":"00","unit":"小时"},{"id":7508,"memo":"提前预约什么时间服务：扶送晒太阳服务","name":"扶送晒太阳","price":"00","unit":"小时"},{"id":7509,"memo":"提前预约什么时间服务：娱乐服务","name":"唱歌给老人听","price":"00","unit":"小时"}],[{"id":7429,"memo":"空调清洗，保养， 100元/台， 编码2001","name":"空调维修2001","price":"100","unit":"元/台"},{"id":7487,"memo":"","name":"政府购买服务","price":"0","unit":"小时"},{"id":7488,"memo":"","name":"测试","price":"0.01","unit":"元"}],[{"id":5683,"memo":"0.01元","name":"商品销售0.01","price":"0.01","unit":"元"},{"id":5684,"memo":"","name":"商品销售0.1","price":"0.1","unit":"元"},{"id":5685,"memo":"","name":"商品销售1","price":"1","unit":"元"},{"id":5686,"memo":"50元","name":"商品销售50","price":"50","unit":"元"},{"id":5687,"memo":"10元","name":"商品销售10","price":"10","unit":"元"}]],
           // sort:["养老服务","生活服务","购物商城"],
           timer:0,
           toScroll:-1,
      }
  },created(){
      this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
      this.vcode = c.getLocal('yt-vcode');
      window.loginCallback = this.loginCallback;
      window.itvServicesCallBack = this.itvServicesCallBack;
      window.loginTimeCallback = this.loginTimeCallback;
      window.getServicesTime = this.getServicesTime;
      window.getYtItems = this.getYtItems;
      window.servicesItems = this.servicesItems;
      window.userkeepTime = this.userkeepTime;
      window.queryUserInfo = this.queryUserInfo;
      if(c.isAllNull(this.vcode)){
          // 暂无vcode 需登录
          ytone.getServerTime(ytone.keepTime,'loginTimeCallback');
      }else{
          this.vcode = c.getLocal('yt-vcode');
          //  获取服务商
          ytone.getServerTime(ytone.keepTime,'getServicesTime');
          //  获取用户信息
          ytone.getServerTime(ytone.keepTime,'userkeepTime');
      }
  },mounted(){

  },activated(){
      this.$store.commit('GET_HTTPRESPONSETIME',new Date().getTime());
     // Toast("启用组件缓存");
  },methods:{
      keyCode(kc) {
        if($('.familyService').is(':animated'))return;
        if (kc == "KeyBack") {
            this.$router.go(-1);
        }else if(kc == "right"){
                    this.right();
        }else if(kc == "left"){
                    this.left();
        }else if(kc == "down"){
                    this.down();
        }else if(kc == "up"){
                    this.up();
        }else if(kc == "KeyEnter"){
            if(this.pos==2){
                this.$router.push({name:'index'});
            }else if(this.pos==1){
                try{
                    this.pageId = this.services[this.lastF].id;
                    let traceId = "|"+this.services[this.lastF].id+"|";
                    let traceType = "|1|";
                    bi.categoryClick(this.services[this.lastF].id, this.section[this.itemNo].id, 3, 1, this.services[this.lastF].id, traceId, traceType);
                } catch (error) {
                    console.log(error);
                }
                const ele = this.section[this.itemNo];
                const facilitator_id = this.services[this.lastF].id;
                this.$router.push({name:'ytDetail',params:{productInfo:ele,facilitator_id:facilitator_id}});
            }
        }
      },right(){
          if(this.pos==0){
              this.removeCss();
              $('.lfocus').eq(this.itemNo).show();
              $('.serviceName li').eq(this.itemNo).addClass('color');
              this.pos=1;
              this.lastF = this.itemNo;
              this.itemNo=0;
              this.addCss();
          }else if(this.pos==1){
              if(this.itemNo==2||(this.itemNo%3==2))return;
              if(this.itemNo==this.section.length-1)return;
              this.removeCss();
              this.itemNo++;
              this.addCss();
          }
      },left(){
          if(this.pos==0){
              
          }else if(this.pos==1){
              if(this.itemNo%3==0){
                this.removeCss();
                $('.lfocus').eq(this.lastF).hide();
                $('.serviceName li').eq(this.lastF).removeClass('color');
                $('.familyService').stop(true,true).animate({'top':'130px'},200);
                this.pos=0;
                this.itemNo=this.lastF;
                this.addCss();
              }else{
                this.removeCss();
                this.itemNo--;
                this.addCss();
              }
              
          }
      },down(){
          if(this.pos==0){
              if(this.itemNo==(this.services.length-1))return;
              this.removeCss();
              this.itemNo++;
              this.cid++;
              ytone.getServerTime(ytone.keepTime,'getYtItems');
              this.addCss();
          }else if(this.pos==1){
              if((this.itemNo+3>this.section.length-1))return;
              this.removeCss();
              this.itemNo+=3;
              if(this.itemNo>5){
                  let _this = this;
                  $('.familyService').stop(true,true).animate({'top':'-=200px'},200,function(){
                     _this.pageSize++;
                 });
              }
              this.addCss();
          }else if(this.pos==2){
              this.removeCss();
              this.pos=0;
              this.itemNo=0;
              this.addCss();
          }
      },up(){
          if(this.pos==0){
             this.removeCss();
             if(this.itemNo==0){
                 this.pos=2;
             }else{
                 this.cid--;
                 this.itemNo--;
                 ytone.getServerTime(ytone.keepTime,'getYtItems');
             }
             this.addCss();
          }else if(this.pos==1){
              if(this.itemNo<3)return;
              this.removeCss();
              this.itemNo-=3;
              if(this.itemNo>2){
                let _this = this;
                $('.familyService').stop(true,true).animate({'top':'+=200px'},200,function(){
                     _this.pageSize--;
                });
              }
              this.addCss();
          }
      },addCss(){
          if(this.pos==0){
              let word = this.services[this.itemNo].name.length;
              if(word>8){
                   this.toScroll = this.itemNo;
                   let speed = 40;
                   let that = this;
                   let tab =   document.querySelector('#services-'+this.itemNo);
                   let tab1 =  document.querySelector('#yt-services-'+this.itemNo);
                   let tab2 =  document.querySelector('#yt-copy-'+this.itemNo);
                    tab.style.textOverflow = 'clip';
                   this.timer = setInterval(function(){
                        that.Marquee(tab,tab1,tab2)
                  },speed);
              }
              $('.serviceName li').eq(this.itemNo).addClass('hover');
          }else if(this.pos==1){
              $('.familyService li').eq(this.itemNo).addClass('hover');
          }else if(this.pos==2){
              $('.yt-back').addClass('hover');
          }
      },removeCss(){
          if(this.pos==0){
            if(this.timer!=0){
              clearInterval(this.timer);
              this.toScroll = -1;
              let tab =   document.querySelector('#services-'+this.itemNo);
              tab.scrollLeft = 0;
              tab.style.textOverflow = 'ellipsis';
            }
              $('.serviceName li').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==1){
              $('.familyService li').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==2){
              $('.yt-back').removeClass('hover');
          }
      },loginCallback(info){ // 援通账号登录
        console.log("YT return："+JSON.stringify(info));
        if(info.code==1000){
            console.log("援通账号，登录成功！");
            c.setLocal('yt-vcode',info.vcode);
            this.vcode = info.vcode;
            ytone.getServerTime(ytone.keepTime,'getServicesTime');
            ytone.getServerTime(ytone.keepTime,'userkeepTime');
        }else{  // 登录失败
            console.log("提示用户登录不成功，弹出提示消息");
            Toast({
                message:"援通账号登录失败,请联系客服",
                position:'bottom',
                duration:'10000'
             })
        }
      },itvServicesCallBack(ob){ // 获取服务商信息
           console.log("YT return："+JSON.stringify(ob));
           if(ob.code==1000){
               console.log("获取服务商接口成功!");
               this.services = ob.items;
               ytone.getServerTime(ytone.keepTime,'getYtItems');
           }else{
               console.log("援通服务商接口返回失败");
           }
           
      },loginTimeCallback(ob){ //登录时间同步接口
            console.log("YT return："+JSON.stringify(ob));
            if(ob.code==1000){
                let timespace= ob.no;
                let pwd = ytone.Pwd.toLowerCase();
                let key = ytone.Key.substring(0,8);
                let desPwd = ytone.descEncode(key,pwd);
                let MD5String = ytone.md5(ytone.account+ytone.Pwd+timespace+ytone.Key).toUpperCase();
                let jsonOb = '{\"signature\":\"'+MD5String+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"password\":\"'+desPwd+'\"}';
                console.log("登录json字符串:"+jsonOb);
                ytone.ytonePost(ytone.loginUrl,jsonOb,'loginCallback');
            }else{
                console.log("获取登录时间失败",ob.msg);
            }
           
      },getServicesTime(ob){ // 服务商接口时间同步
           console.log("YT return："+JSON.stringify(ob));
           if(ob.code==1000){
               let timespace= ob.no;
               const md5 = ytone.md5(ytone.account+1+20+0+timespace+this.vcode+ytone.Key).toUpperCase();
               let jsonOb = '{\"signature\":\"'+md5+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"pageidx\":1,\"pagesize\":20,\"seller_id\":0}';
               console.log("请求ITV服务商:"+jsonOb);
               ytone.ytonePost(ytone.services,jsonOb,'itvServicesCallBack');
          }else{
              console.log("获取服务商时间同步接口失败",ob.msg);
           }
           
           
      },getYtItems(ob){ // 获取服务商项目时间同步
            if(ob.code==1000){
                let timespace= ob.no;
                const element = this.services[this.itemNo];
                const seller_id = element.id;
                const md5 = ytone.md5(ytone.account + seller_id  + timespace + this.vcode+ ytone.Key);
                let jsonOb = '{\"signature\":\"'+md5+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"vcode\":\"'+this.vcode+'\",\"pageidx\":0,\"pagesize\":0,\"seller_id\":\"'+seller_id+'\"}';
                console.log("请求ITV服务商项目:"+jsonOb);
                ytone.ytonePost(ytone.items,jsonOb,'servicesItems');
            }
      },servicesItems(jsonData){ // 处理服务项目
          console.log("服务商项目："+JSON.stringify(jsonData))
          // 获取服务商项目
          if(jsonData.code==1000){
              this.section=jsonData.items;
          }
      },userkeepTime(ob){// 获取用户信息
             if(ob.code==1000){
                const deviceNo = "s639963222";
                console.log("设备号:"+deviceNo);
                const timespace = ob.no;
                let md5 = ytone.md5(ytone.account+deviceNo+timespace+ytone.Key+this.vcode).toUpperCase();
                let jsonOb = '{\"signature\":\"'+md5+'\",\"timespace\":\"'+timespace+'\",\"account\":\"'+ytone.account+'\",\"deviceNo\":\"'+deviceNo+'\",\"vcode\":\"'+this.vcode+'\"}';
                ytone.ytonePost(ytone.devicesInfo,jsonOb,'queryUserInfo');
            }else{
                Toast("获取用户信息失败");
            }
      },queryUserInfo(jsonData){ // 处理用户信息
        console.log("YT return："+JSON.stringify(jsonData));
        if(jsonData.code==1000){ //将用户存入本地缓存
            this.userInfo = jsonData;
            c.setLocal('yt-user',JSON.stringify(jsonData));
        }
      },Marquee(tab,tab1,tab2){
        if(tab2.offsetWidth - tab.scrollLeft <=0){ //  文字溢出滚动
                  // tab.scrollLeft -= tab1.offsetWidth;
            tab.scrollLeft  = 0; // 上面的写法会导致滚动时突然抖动
        }else{
            tab.scrollLeft++;
        }
      }
  },watch:{
     services(){
           this.$nextTick(function() {
               console.log("更新类目");
                this.addCss();
            });
     }
  },beforeRouteLeave(to, from, next){
      try {
           this.$store.commit('GET_STARTTIME',new Date().getTime());
           let overTime = 0;
           overTime =  new Date().getTime() - this.httpResponseTime;
           let  focusId = this.services[this.lastF].id;
           bi.closPage(focusId,1,1,focusId,0,0,0,overTime)
      } catch (error) {
           console.log(error);
      }finally{
           next();
      }
    },computed:{
        ...mapGetters([
        'category',
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
        'navpos'
      ])
    }
}
</script>



