// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/store'
import keyDo from '../src/components/keyCode/index.js'
import VueLazyImage from '../src/tools/lazyLoad/index.js'
import LFirm from '../src/tools/Firm/index.js'
import {client as yh} from './js/client.js'

Vue.config.productionTip = false;


Vue.use(LFirm);
Vue.use(keyDo);
Vue.use(VueLazyImage, { debounce: 200 });

router.afterEach((to,from)=>{
  if (from.name!=null){
    store.commit('GET_STARTTIME', new Date().getTime());
  }
  console.log("%cMac:", "color:red", "组件已渲染");
});

router.beforeEach((to, from, next) => {
  console.log(to);
  if(to.name=="index"){
    //==================================================认证模块
    window.localStorage.setItem('MacList', "88cc4526b11c,66666666,3cbd3ef7863a,009ec8e037ff,ac4afeb380cd,38a28c177ab8");
    const MacList = window.localStorage.getItem('MacList');
    console.log("认证列表:" + MacList);
    let macs = [];
    if (MacList != undefined && MacList!=""){
       macs = MacList.split(',');
    }
    let auth = false;
    // const Mac = yh.Mac();
    const Mac ="66666666";
   if (macs.length > 0) {
      console.log(Mac);
      for (let index = 0; index < macs.length; index++) {
        const element = macs[index];
        if (element == Mac) {
          console.log("认证通过!");
          auth = true;
        }
      }
    if (!auth) {
      window.swal({
        title: "设备认证失败，请联系客服解决",
        text: "10s以后自动退出",
        timer: 10000,
        showConfirmButton: false
      });
      setTimeout(() => {
        yh.back();
      }, 10000);
    }else{
      next();
    }
  }else{
      console.log("获取mac认证列表异常");
      window.swal({
        title: "设备认证失败，请联系客服解决",
        text: "10s以后自动退出",
        timer: 10000,
        showConfirmButton: false
      });
      setTimeout(() => {
        yh.back();
      }, 10000);
  }
      //=========================================================
  }else{
    next()
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



