<!--详情页——游戏类详情页顶部-->
<style scoped="DTop">
  .Rectangle{
    width:1120px;
    height: 440px;
    background: #100B1B;
    padding-left: 34px;
    padding-top: 22px;
    border-radius: 15px;
  }
  .Rectangle:after{
    clear: both;
    content: "";
    display: block;
  }
  .Rectangle .Mask{
    border-radius:15px;
    border: #707070 1px solid;
    width:266px;
    height:366.36px;
    float: left;
    position: relative;
  }
  .Rectangle .Mask img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    border:2px solid #ffffff;
    box-sizing: border-box;
  }
  .Bona{
    width: 700px;
    margin-left: 46px;
    float: left;
  }
  .Bona .text-page{
    text-align: left;
  }
  .Bona .text-page span:nth-child(1){
    font-family:PingFangSC-Semibold;
    font-size:32px;
    color:#ececec;
    margin-right: 5px;
  }
  .Bona .text-page span:nth-child(2){
    font-family:PingFangSC-Medium;
    font-size:20px;
    color:#f5a623;
  }
  .Bona .text-page2 {
    font-family:PingFangSC-Regular;
    font-size:20px;
    color:rgba(255,255,255,0.8);
    text-align:left;
    margin-top: 17px;
  }
  .Bona .text-page2:after{
    clear: both;
    content: '';
    display: block;
  }
  .Bona .text-page2 span{
    border-left: 3px solid rgba(255,255,255,0.8);
    text-indent: 10px;
    display:block;
    float: left;
    margin-right: 10px;

  }
  .Bona .text-page2 span:nth-child(1){
    border: none;
    text-indent: 1px;
  }
  .Bona  .content-page3{
    font-family:PingFangSC-Regular;
    text-indent: 10px;
    font-size:22px;
    color:#ffffff;
    text-align:left;
    margin-top: 34px;
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    opacity: 0.8;
    width: 730px;
  }
  .btn-page{
    width: 250px;
    position: absolute;
    left:346px;
    top: 408px;
  }
  .btn-page div:nth-child(1){
    border-radius: 15px;
    background:rgba(255,255,255,0.2);
    font-size:20px;
    color:rgba(255,255,255,0.8);
    text-align:center;
    float: left;
    line-height: 46px;
    width: 120px;
    height: 45px;
  }
  .btn-page div:nth-child(2){
    margin-left: 10px;
    border-radius: 15px;
    background:rgba(255,255,255,0.2);
    font-size:20px;
    color:rgba(255,255,255,0.8);
    text-align:center;
    float: left;
    line-height: 46px;
    width: 120px;
    height: 45px;
  }
  .oll-page li{
    font-family:PingFangSC-Regular;
    font-size:18px;
    color:#ffffff;
    float: left;
    padding-left: 42px;
    padding-top: 14px;
  }
  .Rectangle .btn-page .change{
    background: #DE6B04;
    border: solid 4px #FFD201;
    box-sizing: border-box;
    line-height: 38px;
  }

</style>
<template>
  <div class="Rectangle" style="box-sizing: border-box;">
    <div class="Mask">
      <img :src=(art.assetImg) >
    </div>
    <div class="Bona">
      <p class="text-page">
        <span>{{art.assetName}}</span>
        <span>{{art.score}}</span>
      </p>
      <p class="text-page2">
        <span>{{art.area}}</span>
        <span>{{art.year}}</span>
        <span>{{art.language}}</span>
        <span>{{art.episodes+"集"}}</span>
      </p>
      <p class="content-page3">{{art.description}}</p>
      <div class="btn-page">
        <div class="btn-play" v-if="artCc">
          <i class="iconfont">&#xe607 </i>
          播放</div>
        <div class="btn-js "><i class="iconfont">&#xe60b    </i>介&#160绍</div>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow" ref="keyDo"></keyDo>
  </div>
</template>
<script>
  import {client as yh} from '../../js/client.js'
  import { mapGetters } from 'vuex'
  import {bp as bi} from '../../js/ga.js'

  export default {
    props:['jsonUrl','initData',"catCode","catId","parent","parentId","navpos"],
    data() {
      return {
        name:'DetailTop',
        num:0,
        isShow:false,
        pos:'',
        str:'',
        artCc:false,
        itemNo:0,
        art: {
        },
        collect:
          {

            asType:'',
            asimg:'',
            asname:'',
            sco:'',
            asId:''
          },
      }
    },
    components: {
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    created(){
      this.art = this.initData.data;
      console.log(this.art.itemList.length+"集数个数");
      if(this.art.itemList.length ==1){
          this.artCc = true;
      }
    },
    updated(){

    },
    methods: {
      keyCode(kc) {
        if (kc == "down") {
          this.down();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "KeyEnter") {
          this.KeyEnter();
        }else if(kc == "KeyBack"){
          this.back();
        } else if (kc == "up") {
          this.up();
        }
      },
      KeyEnter() {
        if(this.itemNo==1){
          console.log(this.art.assetName);
          let info = {title: this.art.assetName, msg: this.art.description, alert: true};
          this.isShow = false;
          this.$emit('alertM', info);
          $('.btn-js').removeClass('change');
        }else {
          console.log(this.navpos+'数目');
          let tempUrl = this.jsonUrl.replace('https://winnow-bds.yanhuamedia.tv','http://winnow-ds.yanhuamedia.tv');
          let obj = JSON.stringify({catId:this.navpos,index:this.num,startTime:0,jsonUrl:tempUrl});
          let id = this.initData.data.itemList[0].itemId;
          console.log(id+'下方推荐');
          let pageId =this.initData.data.assetId;
          let traceId = "|"+this.catId;
          let traceType = this.catCode;
          //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
          bi.categoryClick(this.catId,id,3,31,pageId,traceId,traceType);
          console.log(this.catId,id,5,31,pageId,traceId,traceType+'点击事件');
          yh.play(obj);
          console.log(obj);
        }
//        console.log(JSON.stringify(this.art)+"穿过来的数据");
        let id = this.initData.data.itemList[0].itemId;
        let pageId = this.initData.data.assetId;
        let traceId = "|" + this.catId;
        let traceType = this.catCode;
        //      bp.categoryClick = function (cat_id, cat_elements, click_type, pageType, pageId, traceId, traceType)
        bi.categoryClick(this.catId, id, 3, 31, pageId, traceId, traceType);
        console.log(this.catId, id, 5, 31, pageId, traceId, traceType + '点击事件');
      }
      , back() {
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      left(){
        $('.btn-js').removeClass('change');
        this.itemNo=0;
        $('.btn-play').addClass('change');
      },
      right(){
        $('.btn-play').removeClass('change');
        this.itemNo=1;
        $('.btn-js').addClass('change');
      },
      down(){
        $('.btn-play').removeClass('change');
        $('.btn-js').removeClass('change');
        this.isShow = false;
        this.$emit('pressDown', this.isShow,this.name,"down");
      },
      up(){
        $('.btn-play').removeClass('change');
        $('.btn-js').removeClass('change');
        this.isShow = false;
        this.$emit('pressDown', this.isShow,this.name,"up");
      },
      updateStast(bl,pos){
        this.isShow = bl;
        this.pos = pos;
        if(bl){
          if(this.artCc){
            this.itemNo=0;
            $('.btn-play').addClass('change');
            let eleH = this.cEleOffsetTop('.btn-js');
            this.$emit('eleScroll',this.name,pos,eleH);
          }else {
            this.itemNo=1;
            $('.btn-js').addClass('change');
            let eleH = this.cEleOffsetTop('.btn-js');
            this.$emit('eleScroll',this.name,pos,eleH);
          }
        }
      },cEleOffsetTop(ele){
        let fEleDistance = $(ele).offset();
        console.log(fEleDistance+'gaokaundi');
        let realHeight = fEleDistance.top+300;
        return realHeight;
      },popUp(bOk) {

      },
      changeLong(bl,pos){

      },
      findindex(){
        let storage =JSON.parse(localStorage.getItem('PersonInfo'));
        for(let i = 0;i<storage.length;i++){
          if (storage[i].assetId == this.art.assetId){
            return i;
          }
        }
      },
      Coll(){
        let pageId = this.art.assetId;
        let assId = this.art.itemList[0].itemId;
        let traceId =  this.catId;
        let traceType = this.catCode;
//        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,1);
        console.log(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,1);
      },
      NoColl(){
        let pageId =this.art.assetId;
        let assId = this.art.itemList[0].itemId;
        let traceId =  this.catId;
        let traceType =  this.catCode;
//        bp.collect = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id, cat_id, asset_id, collect_type, type)
        bi.collect(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,2);
        console.log(traceId,traceType,31,pageId,traceType,this.catId,pageId,assId,3,2);
      }
    },
    watch:{
      initData(){
        this.art = this.initData.data;
        this.isShow = false;
        $('.btn-play').removeClass('change');
//        if (this.queryFavorite()){
//          this.sc = true;
//          this.ss = false
//        }else {
//          this.sc = false;
//          this.ss = true;
//        }
      }
    }
  }
</script>



