<template>
  <div id="app">
    <!-- 这里是需要keepalive的 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"/>
    </keep-alive>
    <!-- 这里不会被keepalive -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
import {client as yh} from './js/client'
   
export default {
  name: 'App',
  created(){
      document.title = "视嘉 TV爱爸妈";
  },mounted(){
     setTimeout(() => {
        let el = document.getElementById('openAd');
        console.log(el);
        if (el) {
              el.addEventListener('webkitAnimationIteration', function(){
                  console.log('loading 3s 结束 webkitAnimationIteration');
                  this.style.opacity=0;
                   setTimeout(() => {
                      console.log("隐藏动画");
                      el.style.display='none';
                  }, 100);
              })
              el.addEventListener("animationiteration", function(){
                console.log('loading 3s 结束 animationiteration');
                this.style.opacity=0;
                 setTimeout(() => {
                      console.log("隐藏动画");
                      el.style.display='none';
                  }, 100);
               
              });
        }
    }, 3000);
  },methods:{

  }
}
</script>

<style>
@import url("./fonts/iconfont.css");
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    background:linear-gradient(127deg, rgba(51, 61, 87, 1) 10%, rgba(15, 25, 57, 1) 70%,rgba(15, 25, 57, 1) 90%,rgba(15, 25, 57, 1) 100%);
    position: absolute;
    width: 1280px;
    height: 720px;
  }
</style>



