<template>
  <div class="OtherService">
      <div class="videoRecord">
          <div class="record">播放记录</div>
          <ul class="record-list">
              <li v-for="(item,index) in historyList" :key="index" v-if="index<4"><img :src='item.assetImg' /></li>
          </ul>
          <div class="Gohistory iconfont icon-clickRight">播放记录</div>
      </div>
      <div class="FourService">
          <ul>
            <li><img src="../../assets/Intersection 1.png">居家服务</li>
            <li><img src="../../assets/Intersection 2.png">电视购物</li>
            <li><img src="../../assets/Intersection 4.png">援通服务</li>
            <li><img src="../../assets/Intersection 3.png">个人中心</li>
          </ul>
      </div>
      <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped>
    .OtherService{position: relative;width: 1180px;height: 235px;margin-top:20px;}
    .videoRecord{position: relative;width:792px;height:230px;float: left;border-radius:10px;;background-color:rgba(255, 255, 255, 0.12);text-align:left;}
    .record{position: relative;display: inline-block;height:130px;line-height:35px;width:37px;word-wrap: break-word;letter-spacing:27px;font-size:22px;color:#FFFFFF;background-color:#333D57;padding:10px 0px 20px 12px;top: 40px;border-radius:0px 10px 10px 0px;float: left;}
    .record-list{position: relative;width: 642px;height: 230px;display: inline-block;margin-left:20px;text-align: center;float: left;}
    .record-list li{width:140px;height:205px;margin-top:15px;float:left;margin-right:20px;}
    .record-list li img{width: 100%;height:100%;border-radius:13px;}
    .Gohistory{position:relative;display:inline-block;height:180px;line-height:32px;font-size:22px;width:40px; background: linear-gradient(90deg, #485881, #242C41); ;word-wrap: break-word;letter-spacing:30px;padding:15px 0px 10px 15px;top: 12px;border-radius:16px;border:1px solid rgba(255, 255, 255, 0.2);text-align: center;color: #FFFFFF;}
    .FourService{position: relative;display: inline-block;height:225px;width:350px;margin-left:25px;}
    .FourService ul li{width: 125px;height: 96px;font-weight:550; float: left;margin-top:14px;margin-right:24px;color: rgba(255, 255, 255, 0.8);font-size:16px; text-align: end;line-height: 156px;padding-right:10px;letter-spacing:2px;border-radius: 15px;position: relative}
    .FourService ul li img:nth-child(1){
      position: absolute;
      left: 0px;
      top: 8px;
    }
    .FourService ul li:nth-child(1) img{
      width: 74px;
      height: 77px;
    }
    .FourService ul li:nth-child(2) img{
      width: 64px;
      height: 88px;
    }
    .FourService ul li:nth-child(3) img{
      width: 61px;
      height: 70px;
    }
    .FourService ul li:nth-child(4) img{
      width: 57px;
      height: 80px;
    }
    .FourService ul li:nth-child(1){background:linear-gradient(180deg, #3A8A8D, #113538);}
    .FourService ul li:nth-child(2){background:linear-gradient(180deg, #C97400, #C97400);}
    .FourService ul li:nth-child(3){background:linear-gradient(180deg, #AC4A4A, #461919);}
    .FourService ul li:nth-child(4){background:linear-gradient(180deg, #1D1B4E, #2D487C);}
    .record-list li.hover,.FourService li.hover{border:6px solid #FFD800;margin:9px 14px -6px -6px;border-radius:15px;}
    .Gohistory.hover{border:6px solid #FFD800;margin: -5px;}
    .FourService ul li.hover{border:6px solid #FFD800;margin: 8px 18px -6px -6px;}
</style>
<script>
import {client as yh} from '../../js/client.js'
import {bp as bi} from '../../js/ga.js'
import {mapGetters} from 'vuex'

export default {
  name:'OtherServices',
  props:['catId'],
  data(){
      return{
          isShow:false,
          eleId:"00001",
          itemNo:0,
          historyList:[],
          pos:0,
          placeholder:require('../../assets/Intersection 1.png'),
      }
  },created(){
      this.initHistory();
  },activated(){
      console.log("组件缓存激活");
      this.initHistory();
  },computed: {
      ...mapGetters([
        'category',
        'startTime',
        'httpRequestTime',
        'httpResponseTime',
        'navpos'
      ])
    },methods:{
      keyCode(kc){
          if (kc == "KeyBack") {
                this.back()
          }else if(kc == "right"){
                this.right();
          }else if(kc == "left"){
                this.left();
          }else if(kc == "down"){
                this.down();
          }else if(kc == "up"){
                this.up();
          }else if(kc == "KeyEnter"){
              debugger
               this.$store.commit('GET_STARTTIME',new Date().getTime());
               try {
                        this.pageId = this.catId;
                        let traceId = "|"+this.catId+"|";
                        let traceType = "|1|";
                        bi.categoryClick(this.catId,'', 3, 1, this.catId, traceId, traceType);
                } catch (error) {
                        console.log(error);
                }
              if(this.pos == 0){
                  if(this.itemNo==4){
                    this.$router.push({name:"history",params:{catCode:6,catId:202642}});
                  }else {
                    let url = this.historyList[this.itemNo].layoutUrl;
                    this.$router.push({name:"Detail",params:{jsonUrl:url,catCode:3,catId:this.catId,parent:1,parentId:this.catId}});
                  }
              }else{
                if (this.itemNo ==3){
                  this.$router.push({name:'ytOrder'});
                }else {
                  console.log("援通服务页");
                  this.$router.push({name:'ytone'});
                }
              }
          }
      },back() {
        this.removeCss();
        this.isShow = false;
        this.itemNo = 0;
        this.pos =0;
        this.$emit('pressDown', this.isShow,this.eleId,"back");
      },right(){
          this.removeCss();
          if(this.pos==0){
              if(this.itemNo==4){
                  this.pos=1;
                  this.itemNo=0;
              }else{
                  if((this.itemNo+1)==this.historyList.length){
                      this.itemNo = 4;
                  }else{
                      this.itemNo++;
                  }
              }
          }else if(this.pos==1){
              if(this.itemNo==3){
                }else{
                this.itemNo++;
              }
            }
            this.addCss();
        },left(){
          if(this.itemNo==0 && this.pos==0)return;
          if(this.historyList.length==0&&this.pos==0)return;
          this.removeCss();
          if(this.pos==1){
              if(this.itemNo==0||this.itemNo==2){
                  this.pos=0;
                  this.itemNo=4;
              }else{
                  this.itemNo--;
              }
          }else{
            if(this.historyList.length>=4){
                this.itemNo--;
            }else{
                if(this.itemNo<this.historyList.length){
                    this.itemNo--;
                }else{
                    this.itemNo = this.historyList.length-1;
                }
            }
          }
          this.addCss();
      },down(){
          this.removeCss();
          if(this.pos==1&&this.itemNo<2){
              this.itemNo+=2;
          }
          this.addCss();
      },up(){
          this.removeCss();
          if(this.pos==1&&this.itemNo>1){
              this.itemNo-=2;
              this.addCss();
          }else{
              this.isShow = false;
              this.$emit('pressDown', this.isShow,this.eleId,"up");
          }
      },addCss(){
          if(this.pos==0){
              if(this.itemNo<4){
                  $('.record-list li').eq(this.itemNo).addClass('hover');
              }else{
                  $('.Gohistory').addClass('hover');
              }
          }else if(this.pos==1){
                  $('.FourService li').eq(this.itemNo).addClass('hover');
          }
      },removeCss(){
          if(this.pos==0){
              if(this.itemNo<4){
                  $('.record-list li').eq(this.itemNo).removeClass('hover');
              }else{
                  $('.Gohistory').removeClass('hover');
              }
          }else if(this.pos==1){
                 $('.FourService li').eq(this.itemNo).removeClass('hover');
          }
      },
      updateStast(bl,pos,isEnd){
        this.isShow = bl;
        if(bl){
            if(this.historyList.length==0){
                this.pos = 0;
                this.itemNo = 4;
            }
             this.addCss();
        }
    },
    initHistory(){
        // 获取点播历史信息
        let jsonObject = yh.queryHistory();
        console.log("点播记录:"+jsonObject);
        try {
            let arr = JSON.parse(jsonObject);
            this.historyList =arr.reverse();
            let _tempArray = [];
            if(this.historyList.length>3){
                let _this = this;//  sort 作用域在windows下面需要注意
                let sortedObjKeys = Object.keys(this.historyList).sort(function(a, b) {
                  return _this.historyList[b].recordTime - _this.historyList[a].recordTime;
                });
                for (let index in sortedObjKeys) {
                    if(sortedObjKeys.hasOwnProperty(i)){
                        _tempArray.push(this.historyList[sortedObjKeys[index]]);
                    }
                }
                this.historyList  = _tempArray;
                this.historyList = this.historyList.slice(0,4);
            }
        } catch (error) {
            console.log(error)
            console.log("获取历史记录异常");
        }
    }
  }
}
</script>



