<template>
  <div class="loader" v-if="isload" >
    <div class="loading-1" >
            <i></i>
            <i></i>
            <i></i>
            <i></i>
            <i></i>
            <i></i>
            <i></i>
            <i></i>
    </div>
  </div>
</template>
<style scoped>
     @-webkit-keyframes loading-1 {
        50% {
            transform: scale(.4);
            opacity: .3
        }
        100% {
            transform: scale(1);
            opacity: 1
        }
    }
    .loading-1 {
        position: relative
    }

    .loading-1 i {
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: #ffffff;
        position: absolute
    }

    .loading-1 i:nth-child(1) {
        top: 25px;
        left: 0;
        -webkit-animation: loading-1 1s ease 0s infinite
    }

    .loading-1 i:nth-child(2) {
        top: 17px;
        left: 17px;
        -webkit-animation: loading-1 1s ease -.12s infinite
    }

    .loading-1 i:nth-child(3) {
        top: 0;
        left: 25px;
        -webkit-animation: loading-1 1s ease -.24s infinite
    }

    .loading-1 i:nth-child(4) {
        top: -17px;
        left: 17px;
        -webkit-animation: loading-1 1s ease -.36s infinite
    }

    .loading-1 i:nth-child(5) {
        top: -25px;
        left: 0;
        -webkit-animation: loading-1 1s ease -.48s infinite
    }

    .loading-1 i:nth-child(6) {
        top: -17px;
        left: -17px;
        -webkit-animation: loading-1 1s ease -.6s infinite
    }

    .loading-1 i:nth-child(7) {
        top: 0;
        left: -25px;
        -webkit-animation: loading-1 1s ease -.72s infinite
    }

    .loading-1 i:nth-child(8) {
        top: 17px;
        left: -17px;
        -webkit-animation: loading-1 1s ease -.84s infinite
    }
</style>
<script>
export default {
  props:['isload']
}
</script>



