<!--详情页——双层选集层-->
<template>
  <div style="box-sizing: border-box;margin-top: -64px" class="pt" >
    <div class="ption-topx " id="pt1">
      <div class="ptionx">
        <p v-for="(item,index) in this.a" :key="item.id" v-if="index>=0" :id="('a'+index)" class="text_num">
          {{(item-1)*32+1}}-{{item*32}}
        </p>
        <p class="text_num" :id="('a'+this.a)" v-if="this.acc>(this.a*32)+1">{{(this.a*32)+1}}-{{this.acc}}</p>
      </div>
    </div>
    <div class="ption-top " id="pt2">
      <div class="ption">
        <p v-for="(item,index) in top" :key="item.id" v-if="index>=0" :id="('c'+index)" class="text_num">
          {{index+1}}
        </p>
      </div>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>
<style scoped="list">
  .pt{
    width: 1120px;
    height: 240px;
    background: #100B1B;
    padding-top: 27px;
    padding-left: 34px;
    border-radius: 0px 0px 15px 15px;
  }
  .ption-topx{
    width: 1040px;
    margin-top: 3px;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
  }
  .ption-top{
    width: 1040px;
    height: 114px;
    overflow: hidden;
    margin-top: 3px;
    border-radius: 5px;
    position: relative;
  }
  .ption{
    width: 1065px;
    height: 114px;
    position: absolute;
    left: 0;
    top: 0px;
    transition: all 0.01s;
  }
  .ption .text_num{
    width: 60px;
    height: 50px;
    font-family:PingFangSC-Regular;
    font-size:18px;
    text-align:center;
    margin-right: 5px;
    margin-top: 5px;
    float: left;
    line-height: 50px;
    border-radius: 15px;
    background: rgba(255,255,255,0.2);
    color: white;
    box-sizing: border-box;
  }
  .ptionx{
    width: 1000%;
    transition: all 0.1s;
    height: 60px;
  }
  .ptionx .text_num{
    width: 120px;
    height: 50px;
    font-family:PingFangSC-Regular;
    font-size:26px;
    text-align:center;
    color: rgba(255,255,255,0.8);
    float: left;
    line-height: 50px;
    border-radius: 15px;
    background: rgba(255,255,255,0.2);
    margin-right: 24px;
    box-sizing: border-box;
  }
  .ptionx .text_num.backchange{
    background: #F36100;
    border: solid 4px #F8D203;
    line-height: 42px;
  }
  .ption .text_num.backchange{
    background: #F36100;
    border: solid 4px #F8D203;
    line-height: 42px;
  }
</style>
<script>
  import { mapGetters } from 'vuex'
  import { client as yh} from '../../js/client.js'
  import {bp as bi} from '../../js/ga.js'
  import c from '../../js/common.js'

  export default {
    props:['initData','jsonUrl',"catCode","catId","parent","parentId","navpos"],
    data() {
      return {
        name:'DetailList',
        op:0,
        itemNo: 0,
        num: 0,
        a:0,
        showl:true,
        isShow:true,
        top:
          [
            {
              episode: "",
            }
          ],
        acc:{
          episodes:''
        }
      }
    },
    computed: {
      ...mapGetters([
        'behindParams',
      ])
    },
    updated(){
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.floor(this.numm/32);
      if(this.isShow){
        $('#a'+(this.itemNo)).addClass('backchange');
        console.log(this.itemNo);
      }
      if (this.acc<=16){
        $(".pt").css("height", "180px");
      }else {
        $(".pt").css("height", "240px");
      }

    },
    created(){
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.floor(this.numm/32);
      $('#a'+(this.itemNo)).addClass('backchange');
      this.$nextTick(function () {
        if (this.acc<=16){
          $(".pt").css("height", "180px");
        }else {
          $(".pt").css("height", "240px");
        }
      });
    },
    mounted() {
      this.top = this.initData.data.itemList;
      this.acc = this.initData.data.itemList.length;
      this.numm = parseFloat(this.acc);
      this.a = Math.floor(this.numm/32);
      $('#a'+(this.itemNo)).addClass('backchange');
      if (this.acc<=16){
        $(".pt").css("height", "180px");
      }else {
        $(".pt").css("height", "240px");
      }
    },
    methods: {
      keyCode(kc) {
        if (kc == "KeyBack") {
          this.back();
        } else if (kc == "right") {
          this.right();
        } else if (kc == "left") {
          this.left();
        } else if (kc == "down") {
          this.down();
        } else if (kc == "up") {
          this.up();
        }else if(kc=='KeyEnter'){
          this.KeyEnter();
        }
      },
      back() {
        if(this.$route.query.jsonUrl!=undefined){
          yh.back();
        }else{
          this.$router.go(-1);
        }
      },
      right(){
        if(this.op > 0){
          if (this.num >= this.acc-2){
            this.num = this.acc-2;
          }
          if((this.num+1)%16==0)return;
          this.removeCss();
          this.num++;
          this.addCss();
        }else {
          if(this.acc>(this.a*10)+1){
            if (this.itemNo>=this.a)return;
          }else {
            if (this.itemNo>=this.a-1)return;
          }
          $('#a'+(this.itemNo)).removeClass('backchange');
          this.itemNo++;
          this.num+=32;
          $('.ption').animate({'top': '-='+110},50);
          $('#a'+(this.itemNo)).addClass('backchange');
          let left = this.cEleOffsetLeft('#a'+(this.itemNo));
          console.log(left+'距离左边的距离————————');
          if( left>1500){
            $('.ptionx').stop(false,true).animate({'margin-left': '-='+140},10);
          }
        }
      },
      left(){
        if(this.op>0){
          if((this.num)%16 == 0)return;
          this.removeCss();
          this.num--;
          if (this.num < 0){
            this.num = 0;
          }
          this.addCss();
        }else {
          if(this.itemNo<=0)return;
          $('#a'+(this.itemNo)).removeClass('backchange');
          this.itemNo--;
          this.num-=32;
          $('.ption').animate({'top': '+='+110},50);
          $('#a'+(this.itemNo)).addClass('backchange');
          let left = this.cEleOffsetLeft('#a'+(this.itemNo));
          console.log(left+'距离左边的距离————————');
          if( left<563){
            $('.ptionx').stop(false,true).animate({'margin-left': '+='+140},10);
          }
          if(this.itemNo==0){
            $('.ptionx').stop(false,true).animate({'margin-left': '0px'},10);
          }
        }
        console.log(this.num);
      },
      down(){

        this.removeCss();
        $('#a'+(this.itemNo)).removeClass('backchange');
        if (this.op == 1){
          this.num += 16;
          this.addCss();
          if (this.num>this.acc-1){
            this.removeCss();
            this.num = this.acc-1;
            this.addCss();
            this.op = 0;
          }
        }
        this.op += 1;
        this.addCss();
        if (this.op > 2 || this.num >= this.acc-1) {
          this.isShow = false;
          this.op = 2;
          this.removeCss();
          this.$emit('pressDown', this.isShow, this.name, "down");
        }
      },
      up(){
        this.removeCss();
        if(this.op ==2){
          this.num -= 16;
          this.addCss();
        }else if(this.op == 1){
          this.num == 0;
          $('#a'+(this.itemNo)).addClass('backchange');
        }
        this.op -= 1;
        if (this.op < 0) {
          this.isShow = false;
          this.op = 0;
          this.$emit('pressDown', this.isShow, this.name, "up");
          $('#a'+(this.itemNo)).removeClass('backchange');
        }
      },
      removeCss(){
        $('#c'+(this.num)).removeClass('backchange');
      },
      addCss(){
        $('#c'+(this.num)).addClass('backchange');
      },
      updateStast(bl,pos) {
        this.isShow = true;
//        if(this.acc>1){
//          $(".pt").css("height", "240px");
//          if(pos=='KeyEnter'){
//            this.op = 0;
//            this.itemNo = 0;
//            this.num = 0;
//            $('#a'+(this.itemNo)).addClass('backchange');
//            $('.ption').animate({'top': '0'},50);
//          }else if(bl){
//            if(this.op>0){
//              this.num = this.itemNo*32;
//              this.op = 0;
//              $('#a'+(this.itemNo)).addClass('backchange');
//            }else {
//              $('#a'+(this.itemNo)).addClass('backchange');
//            }
//          }
//        } else {
//          $(".pt").css("height", "120px");
          if(pos=='KeyEnter'){
            this.op = 1;
            this.itemNo = 0;
            this.num = 0;
            $('.ption').animate({'top': '0'},50);
            $('.ptionx').stop(false,true).animate({'margin-left': '0px'},50);
          }else if(bl){
            if(this.op>0){
              this.num = this.itemNo*32;
              this.op = 0;
              $('#a'+(this.itemNo)).addClass('backchange');
            }else {
              $('#a'+(this.itemNo)).addClass('backchange');
            }
          }
      },
      cEleOffsetTop(ele) {
        let fEleDistance = $(ele).offset();
        let realHeight = fEleDistance.top+450;
        return realHeight;
        console.log(fEleDistance+'获取的高度');
      },
      cEleOffsetLeft(ele) {
        let fEleDistance = $(ele).offset();
        let realLeft = fEleDistance.left+450;
        return realLeft;
        console.log(fEleDistance+'距离左边的距离');
      },
      KeyEnter(){
        if(this.op > 0){
          let tempUrl = this.jsonUrl.replace('https://winnow-bds.yanhuamedia.tv','http://winnow-ds.yanhuamedia.tv');
          let obj = {};
          try {
            let id = this.initData.data.itemList[this.num].itemId;
            let pageId =this.initData.data.assetId;
            let traceId = "|"+this.catId;
            let traceType = "|"+this.catCode;
            bi.categoryClick(this.catId,id,5,31,pageId,traceId,traceType);
          } catch (error) {
            console.log("埋点异常");
          }
          let met = yh.playHistory(this.initData.data.assetId);
          if(c.isAllNull(met)){// 历史记录为空时
            obj ={catId:this.navpos,index:this.num,startTime:0,jsonUrl:tempUrl};
          }else{
            if(typeof(met)=="string"){
              met = JSON.parse(met);
            }
            if(met.episode-1 == this.num){
              obj = {catId:this.navpos,index:this.num,startTime:met.watchTime,jsonUrl:tempUrl};
            }else {
              obj ={catId:this.navpos,index:this.num,startTime:0,jsonUrl:tempUrl};
            }
          }

          // if(met){
          //   console.log(JSON.parse(met.watchTime)+'是否记录时间传入');
          //    obj = JSON.stringify({index:this.num,startTime:met.watchTime,jsonUrl:tempUrl});
          // }else {
          //    obj = JSON.stringify({index:this.num,startTime:0,jsonUrl:tempUrl});
          // }

          console.log("detail:"+JSON.stringify(obj));
          yh.play(JSON.stringify(obj));
        }
      }
    },
    watch:{
      initData(){
        this.top = this.initData.data.itemList;
        this.acc = this.initData.data.itemList.length;
        this.numm = parseFloat(this.acc);
        this.a = Math.ceil(this.numm/34);
        this.op = 0;
        this.itemNo = 0;
        this.num = 0;
        $('.ption').animate({'top': '0'},50);
        if(this.acc>1){
          $('#a'+(this.itemNo)).addClass('backchange');

        }else {
          this.addCss();
        }
      }
    },
    components: {

    }

  }

</script>



