<template>
    <div class="center">
      <div class="list">{{tittle.tit}}</div>
      <div class="imgList">
        <div  v-for="(item,index) in itemList"  :key="item.id" :id="('hotImg_'+'_'+index)" class="imgListImg"
             v-if="index<=5" >
          <lazy-image
            :src='item.assetImg'
            :placeholder='placeholder'
            :initScore = 'initScore'
            class="imgList_img"
            :imgClass='HistoryImg'
          />
        </div>
      </div>
     <keyDo v-on:listenKeyCode="keyCode"  v-if="isShow"></keyDo>
    </div>
</template>

<style scoped="interCenter">
  .center{
    margin-top: 26px;
  }
  .list{
    width: 128px;
    height: 45px;
    font-size: 32px;
    text-align: left;
    color: white;
  }
  .imgList:after{
    content: '';
    clear: both;
    display: block;
  }
  .imgList .imgListImg{
    width: 541px;
    height:230px;
    float: left;
    margin-left: 16px;
    margin-right: 16px;
    position: relative;
    margin-top: 38px;
    border-radius: 15px;
  }
  .imgList div img{
    border-radius: 15px;
    width: 100%;
    height: 100%;
  }
  .change{
    border: solid 4px #FFD201;
    box-shadow: 0 0 15px #FFD201;
    transform:scale(1.1);
    box-sizing: border-box;
  }
</style>

<script>
    export default {
        data() {
            return {
              tittle:{
                tit:'往期活动'
              },
              itemNo:0,
              num:0,
              HistoryImg:'HistoryImg',
              name:'interestCenter',
              isShow:false,
              placeholder:require('../../assets/img_loading_160x230.png'),
              initScore:require('../../assets/corner_mark.png'),
              itemList:[
                {
                  assetImg:'http://game-s.yanhuamedia.tv/img/201706/22/11/38/5/2017062211385673788ffd807.jpg',
                  score:'6.0',
                  assetName:'微信扫描二维码，报名参加活动'
                },
                {
                  assetImg:'http://game-s.yanhuamedia.tv/img/2017/9/28/196/196/20170928154903930qdau49qq.jpeg',
                  score:'6.0',
                  assetName:'微信扫描二维码，报名参加活动'
                },
                {
                  assetImg:'http://game-s.yanhuamedia.tv/img/2017/9/11/143/143/20170911120439703cfu5xtg8.jpeg',
                  score:'6.0',
                  assetName:'微信扫描二维码，报名参加活动'
                },
                {
                  assetImg:'http://game-s.yanhuamedia.tv/img/2017/9/11/166/166/2017091112034192gex1v9ce.jpeg',
                  score:'6.0',
                  assetName:'微信扫描二维码，报名参加活动'
                },
              ],
            }
        },
      methods: {
        //      点击事件
        collectPage(){
          this.traceId = "|"+this.catId+"|"+0;
          console.log(this.traceId+'历史页面');
          // bp.collectPage = function(traceId, traceType, pageType, pageId, parent_page_type, parent_page_id,asset_id, trace_id, trace_type, special_type, type)
          bi.collectPage(this.traceId,"|1|6|",6,0,1,this.catId,this.assetId,this.traceId,1,0,this.typeid);
        },
        keyCode(params) {
          if (params == "down") {
            this.down();
          } else if (params == "up") {
            this.up();
          } else if (params == "left") {
            this.left();
          } else if (params == "right") {
            this.right();
          } else if (params == "KeyEnter") {
            if (this.num>-1){
//              this.isShow = false;
//              let url = this.favoriteLists[this.num].list[this.itemNo].layoutUrl;
//              let Id =this.favoriteLists[this.num].list[this.itemNo].assetId;
//              this.assetId = Id;
//              console.log("专题ID"+this.assetId);
//              this.$emit('pressLeft',this.isShow,this.name,"KeyEnter",url,Id,'详情',"|1|6|");
//              this.typeid = 1;
//              console.log("点击时候的typeid"+this.typeid);
//              this.collectPage();
              alert('活动入口')
            }
          } else if (params == "KeyBack") {
            this.KeyBack();
          }
        }, KeyBack() {
          this.removeCss();
          this.isShow = false;
          this.$emit('pressUp', this.isShow,this.eleId,"back");
        },down() {
          this.removeCss();
          this.num++;
          this.itemNo +=2;
          if(this.num>1){
            this.num = 1;
            this.itemNo = 3;
          }
          this.addCss();
          let top = this.cEleOffsetTop('#hotImg_'+'_'+this.itemNo);
          console.log(top);
          this.$emit('pressUp', this.isShow,this.name,"down",top);
        },
        up(){
          this.removeCss();
          this.num--;
          this.itemNo -=2;
          this.addCss();
          if(this.num<0){
            this.isShow = false;
            this.$emit('pressUp', this.isShow,this.name,"up");
          }
        },
        left(){
          if(this.itemNo ==2)return;
          this.removeCss();
          this.itemNo--;
          if (this.itemNo<0){
            this.itemNo = 0;
          }
          this.addCss();
        },
        right(){
          if(this.itemNo ==1)return;
          this.removeCss();
          this.itemNo++;
          if (this.itemNo>this.itemList.length-1){
            this.itemNo = this.itemList.length-1;
          }
          this.addCss();
        },
        addCss(){
          $('#hotImg_'+'_'+this.itemNo).addClass('change');
        },
        removeCss(){
          $('#hotImg_'+'_'+this.itemNo).removeClass('change');
        },
        updateStast(bl,pos){
//        线上
//        this.json = JSON.parse(yh.queryHistory());
          this.num = 0;
          this.itemNo = 0;
          this.addCss();
          this.isShow = bl;
          let top = this.cEleOffsetTop('#hotImg_'+'_'+this.itemNo);
          console.log(top);
          this.$emit('pressUp', this.isShow,this.name,"down",top);
        },
        Data(gettime){
          let newTime =new Date().getTime();
          let timeout = Math.ceil((newTime-gettime)/86400000);
          return timeout;
        },
        ToTime(mss) {
          let hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
          let minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
          let seconds = parseInt((mss % (1000 * 60)) / 1000);
          return hours + ":" + minutes + ":" + seconds;
        },
        timeTime(){
          let Day = this.json;
          console.log(this.ToTime(Day[1].watchTime)+'我们需要的时间');
          console.log(Day[1].watchTime);
          for(let i=0;i<Day.length;i++){
            console.log(Day[i].watchTime.indexOf(":")+'是否存在：');
            if(Day[i].watchTime.indexOf(":")<0){
              Day[i].watchTime = this.ToTime(Day[i].watchTime);
              console.log(Day[i].watchTime+'我们需要的时间');
            }
          }
          console.log(JSON.stringify(Day)+'最终几瓶数据');
          this.json = Day;
        },
        cEleOffsetTop(ele) {
          let fEleDistance = $(ele).offset();
          let realHeight = fEleDistance.top+230;
          return realHeight;
          console.log(fEleDistance+'获取的高度');
        },
      }
    }
</script>



