<template>
  <div class="box">
    <div class="scroll-box">
      <!-- 导航、设置 -->
      <div class="navbar">
        <ul class="programa">
          <li v-for="(item,index) in nav" :key="index" v-if="index<5" :catCode="item.catCode" ><div></div>{{item.catName}}</li>
        </ul>
        <div class="interestAct iconfont icon-huodong" v-if="false">{{nav[5].catName}}</div>
      </div>
      <router-view  ref="childComponents" v-on:navigation="IsNav"  :categoryData="currentData" :focusId="currentData.catId" :lastId="lastId" ></router-view>
    </div>
    <keyDo v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <Alert ref="Alert" v-on:listernpopup="firm"></Alert>
  </div>
</template>
<style scoped="index">
  .box{
    width: 1200px;
    height:658px;
    padding-left:80px;
    padding-top: 62px;
  }
  .box,.scroll-box{position: relative;width: 100%;height: 100%;}
  .navbar,.programa{position: relative;display:block;height:36px;width: 890px;}
  .navbar{width:1200px;}
  .programa {margin-right:0px;float:left;margin-bottom: 22px;}
  .programa li,.setting li{float: left;color: #FFFFFF;padding: 0px 5px;}
  .programa li{text-align:center;color:#C5C4CB;height: 35px; font-size: 25px;font-family: 'PingFang HK','Medium';margin-right:70px;}
  .programa li:nth-child(1){text-align:center;}
  .programa .focus{margin-top:-5px;height:3px;background-color:#FFFFFF;border-radius: 5px 5px 0px 0px;margin-bottom:2px;display:block;}
  .programa .focus:after{content: '';position: relative;border: 6px solid #FFFFFF;border-color: #FFFFFF transparent transparent;width: 0;height: 0;bottom: -9px;left:3%;margin-left: -6px;}
  .programa li.color{color:#FFFFFF;text-shadow:0px 1px 10px rgba(255,255,255,0.5) }
  .interestAct{position: relative;width:185px;height:43px;line-height: 43px;display:inline-block;color: #FFFFFF;font-size:23px;letter-spacing:2px;border-radius:50px;background:linear-gradient(180deg,rgba(255, 255, 255, 0.18),rgba(128, 128, 128, 0.18));margin-top:-10px;margin-left:-40px;  }
  .interestAct.hover{background-color:#C97400;border:6px solid #FFD800;margin: -16px -6px -6px -46px;box-shadow:0px 0px 10px 2px #FFD800;}
  .programa li.hover{border:6px solid #FFD800;margin:-6px 64px -6px -6px;border-radius: 10px;background-color:#C97400;color: #FFFFFF;box-shadow:0px 0px 10px 2px #FFD800;}
</style>
<script>
import c from '../js/common'
import {HTTPUtil as api} from '../fetch/api.js'
import {mapGetters} from 'vuex'
import {bp as bi} from '../js/ga.js'
import {client as yh} from '../js/client.js'
import Alert from '../page/module/Alert.vue'
export default {
  name:'Category',
  data(){
    return{
      currentData:{},
//      nav:[{},{},{},{},{},{'catName':''}],
      nav:[{},{},{},{},{}],
      isShow:true,
      itemNo:0,
      pos:0,
      lastId:0,
    }
  },beforeCreate(){
        
      
  },created(){
    if (c.isAllNull(c.getLocal("Home"))) {
        let _this = this;
        console.log("rootPath:"+this.rootPath);
        let _h = this.rootPath.indexOf('https');
        if( _h >= 0){
            this.$store.commit('GET_ISHTTPS',true);
            localStorage.setItem("ISHTTPS", true);
        }
        api.getTheme(this.rootPath).then((json) => {
          console.log("获取主题数据成功"+JSON.stringify(json));
          _this.$store.commit('GET_PROGRAMA', json.data);
          localStorage.setItem("Home", JSON.stringify(json.data));
          var _cjson = _this.catList.jsonUrl;
          if (!c.isAllNull(_cjson)) { // 获取成功以后立马获取二级栏目数据
            if(_this.isHTTPS && _cjson.indexOf('https')<0 ){
              _cjson =  _cjson.replace('http','https');
            }
            let curl =  _cjson + this.behindParams;
            api.get(curl).then((json)=>{
              if (200 == json.code || "200" == json.code) {
                  console.log("获取二级栏目数据成功:" + json.data.catList);
                  _this.nav = json.data.catList;
                  _this.$store.commit('GET_CATEGORY', json.data.catList)
                  localStorage.setItem("Category", JSON.stringify(json.data.catList));
                  _this.$nextTick(function () {
                    _this.currentData = this.filtrateRouter(this.itemNo);
                  })
                }
                console.log("初始化主题及栏目数据成功,写入缓存成功");
            },(json)=>{
                console.log("请求二级栏目数据失败",json);
            })
          }
         },(json)=>{
          //TODO 处理请求fail
           console.log("请求主题数据失败",json);
        });
      } else {
        console.log("use cache");

      }
  },mounted(){
     if (!c.isAllNull(c.getLocal("Home"))){
          this.$store.commit('GET_NAVPOS', this.itemNo);
          this.nav = this.category;
          let cData = this.filtrateRouter(this.itemNo);
          this.currentData = cData;
     }
    this.$nextTick(function(){
       this.addCss();
       bi.openApp(); // bi数据埋点
        c.startTime();
        console.log("岩华TV老年版");
        console.log("组件初始化完成");
        console.log("组件模块加载完毕...");
        console.log("懒加载初始化开始");
        console.log("版本:v1.1.03");
        console.log("终端信息:"+window.navigator.appVersion);
        console.log("userAgent:"+window.navigator.userAgent);
    })
  },methods:{
      keyCode(kc){
          if (kc == "KeyBack") {
              this.back();
          }else if(kc == "right"){
                this.right();
          }else if(kc == "left"){
                this.left();
          }else if(kc == "down"){
                this.down();
          }else if(kc == "up"){
                this.up();
          }
       },
      back(){
        this.isShow = false;
        this.$refs.Alert.updateStast(true, "back");
      },
//      right(){
//         this.removeCss();
//         if(this.itemNo>3){
//           this.pos=1;
//           this.itemNo=5;
//       }else{
//           this.lastId = this.nav[this.itemNo].catId;
//           this.itemNo++;
//         }
//         this.addCss();
//       },
//    放开兴趣爱好页面
        right(){
          this.removeCss();
          if(this.itemNo>3){
            this.itemNo = 4;
          }else {
            this.lastId = this.nav[this.itemNo].catId;
            this.itemNo++;
          }
            this.addCss();
        }
       ,left(){
         if(this.itemNo==0&&this.pos!=1)return;
         this.removeCss();
         if(this.pos==1){
           alert(1);
           this.pos=0;
           this.itemNo=4;
         }else{
           this.lastId = this.nav[this.itemNo].catId;
           this.itemNo--;
         }
         this.addCss();
       },up(){

       },down(){
          this.removeCss();
          $('.programa li').eq(this.itemNo).addClass('color');
          $('.programa li div').eq(this.itemNo).addClass('focus');
          this.isShow = false;
          let _this = this;
          window.setTimeout(function() {
            _this.$refs.childComponents.updateStast(true, "down", false);
          },50)
       },
        firm(bl){
          if (bl){// 退出应用
            let st = new Date().getTime();
            let end = st - c.getAppStartTime();
            console.log('停留时间'+end);
//            埋点信息
//            bi.closeApp(this.recommendedData.club.specialId,end);
            yh.back();
          }else {
            this.isShow = true;
          }
        },
        addCss(){
          if(this.pos==0){
            $('.programa li').eq(this.itemNo).addClass('hover');
          }else if(this.pos==1){
            $('.interestAct').addClass('hover');
          }
       },removeCss(){
          if(this.pos==0){
            $('.programa li').eq(this.itemNo).removeClass('hover');
          }else if(this.pos==1){
            $('.interestAct').removeClass('hover');
          }
       },IsNav(name, pos, show){
            this.isShow = show;
            $('.programa li').eq(this.itemNo).removeClass('color');
            $('.programa li div').eq(this.itemNo).removeClass('focus');
            this.addCss();
      },filtrateRouter(catCode){
        for(let i=0;i<this.nav.length;i++){
            if(catCode == i){
               return this.nav[i];
            }
        }
      },
  },watch:{
    itemNo(){
      this.$store.commit('GET_NAVPOS', this.itemNo)
      this.currentData = this.nav[this.itemNo];
    }
  },computed: {
      ...mapGetters([
        'loadState',
        'rootPath',
        'catList',
        'category',
        'isHTTPS',
        'behindParams'
      ])
    },
  components: {
    Alert
  },
}
</script>




